import * as THREE from 'three'
import Utils from './Utils'

class SSText {
    constructor(text, xref, xdisp, yref, ydisp, textid) {
        this.text = text;
        this.xref = xref;
        this.xdisp = xdisp;
        this.yref = yref;
        this.ydisp = ydisp;
        this.textid = textid;
    }

    updateForResize = () => {
        //console.log("updateForResize");
        //console.log(this.text);
        switch (this.xref) {
            case 0: // center
                this.text.position.x = this.xdisp;
                break;
            case -1: // left
                this.text.position.x = Utils.getLeftBorder() + this.xdisp;
                break;
            case 1: // right
                this.text.position.x = Utils.getRightBorder() + this.xdisp;
                break;
        }
        switch (this.yref) {
            case 0: // center
                this.text.position.y = this.ydisp;
                break;
            case 1: // top
                this.text.position.y = Utils.getTopBorder() + this.ydisp;
                break;
            case -1: // bottom
                //console.log("updateForResize from bottom");
                this.text.position.y = Utils.getBottomBorder() + this.ydisp;
                break;
        }
    }

    getWidth = () => {
        return this.text.geometry.parameters.width;
    }

    getHeight = () => {
        return this.text.geometry.parameters.height;
    }

    setNewPosition = (xref, xdisp, yref, ydisp) => {
        this.xref = xref;
        this.xdisp = xdisp;
        this.yref = yref;
        this.ydisp = ydisp;
        this.updateForResize();
    }

    setScale = (x, y) => {
        this.text.scale.x = x;
        this.text.scale.y = y;
    }

    static width(text) {
        return text.width
    }
    static height(text) {
        return text.height
    }
}

export default SSText