import * as THREE from 'three'

class Timer {
    constructor(callback) {
        this.callback = callback;
        this.timeOuts = [];
        this.clock = new THREE.Clock();
        this.time = 0;
    }

    addTimeOut = (duration, ref) => {
        this.timeOuts.push({ "duration": duration, "ref": ref, "starttime": this.time });
    }

    removeTimeOut = (ref) => {
        for (let i = 0; i < this.timeOuts.length; ++i) {
            if (this.timeOuts[i]["ref"] == ref) {
                this.timeOuts.splice(i, 1);
                break;
            }
        }
    }

    removeTimeOutAll = (ref) => {
        while (true) {
            let bFound = false;
            for (let i = 0; i < this.timeOuts.length; ++i) {
                if (this.timeOuts[i]["ref"] == ref) {
                    this.timeOuts.splice(i, 1);
                    bFound = true;
                    break;
                }
            }
            if (!bFound)
                break;
        }

    }

    update = () => {
        this.time += this.clock.getDelta();
        for (let i = 0; i < this.timeOuts.length; ++i) {
            if (this.time - this.timeOuts[i]["starttime"] >= this.timeOuts[i]["duration"]) {
                this.callback.onTimeOut(this.timeOuts[i]["ref"]);
                this.timeOuts.splice(i, 1);
                break;
            }
        }
    }

    pause = (value) => {
        this.clock.getDelta();
    }

    clearAll = () => {
        this.timeOuts = [];
    }

    dispose() {
    }
}

export default Timer 
