import Config from './Config.js'

class Logs {
    constructor(sdk) {
        this.sdk = sdk;
    }

    logEventLevelStartDuration = (levelNum) => {   
        let time = Math.floor(Date.now()/1000);
        let key = "startTimeSpentLevel"+levelNum;
        localStorage.setItem(Config.getUniqueKeyFor(key), time);        
    }

    logEventLevelEndDuration = (levelNum) =>{
        let time = Math.floor(Date.now()/1000);
        let key = "startTimeSpentLevel"+levelNum;
        let startTime = parseInt(localStorage.getItem(Config.getUniqueKeyFor(key)));
        let timeSpent = time - startTime;
        this.sdk.setLevelCompleted(levelNum, timeSpent, (levelNum != 0));
    }

    logEventPresentationStartDuration = (levelNum) =>{        
        if(levelNum != 0){                 
            let key = "startPresentationTimeSpentLevel"+levelNum;
            if(this.sdk.getPresentationCompleteForLevel(levelNum) == 1){
                let presentTime = Math.floor(Date.now()/1000);
                let getStartPresentBefore = this.sdk.getPresentationTimespent(levelNum);
                let startPresentationTimeSpentLevel = presentTime - getStartPresentBefore;
                localStorage.setItem(Config.getUniqueKeyFor(key), startPresentationTimeSpentLevel);
            }else{
                let startPresentationTimeSpentLevel = Math.floor(Date.now()/1000);
                localStorage.setItem(Config.getUniqueKeyFor(key), startPresentationTimeSpentLevel);
            }
        }
        this.sdk.setLevelStarted(levelNum);
    }

    logEventPresentationEndDuration = (levelNum) =>{
        if(levelNum != 0){
            let startkey = "startPresentationTimeSpentLevel"+levelNum;
            let endkey = "endPresentationTimeSpentL"+levelNum;
            let presentationTimeSpentLevel1= Math.floor(Date.now()/1000);
            localStorage.setItem(Config.getUniqueKeyFor(endkey), presentationTimeSpentLevel1);
            const startTimeSpentLoadedL1 = parseInt(localStorage.getItem(Config.getUniqueKeyFor(startkey)));
            const presentationTimeSpentL1 = presentationTimeSpentLevel1 - startTimeSpentLoadedL1;
            this.sdk.setPresentationCompleted(levelNum, presentationTimeSpentL1);        
        }
    }

    logCorrectAttempt = (levelNum) => {
        this.sdk.setCorrectAttempts(levelNum);
    }
    
    logWrongAttempt = (levelNum) => {
        this.sdk.setWrongAttempts(levelNum);
    }

    IsLevelCompleted = (levelNum) => {
        return this.sdk.getIfLevelIsCompleted(levelNum);
    }
}

export default Logs