class Config {
    static refWidth = 854;
    static frustumSize = 480;
    static showLetsPlay = false;
    static debug1 = false; // Skip presentation
    static debug2 = false;
    static webbrowser_gameplay = false; // change to "true for web build" and "false for app build"
    static myapp = false;
    static datacapture_alerts = true;
    static startLevel = 0;
    static enable_transalate_animation = true;
    static audioInstructionOnlyOnReplay = false; // change to true if you have enabled audioinstruction in unity and you want the audio to play only on click of audio replay button
    static buildVersion = "v14";
    static game_unique_id = "containing-sound-captin";

    static getUniqueKeyFor(key) {
        return Config.game_unique_id + key;
    }
}

export default Config;  