import * as THREE from "three";
import AssetManager from "./AssetManager";
import Texture from "./Texture";
import Utils from "./Utils";
import Timer from "./Timer";
import SAudio from "./SAudio";
import HintCompleteDialog from "./HintCompleteDialog";
import SkipActionDialog from "./SkipActionDialog";
import BackActionDialog from "./BackActionDialog";
import SSScreen from "./SSScreen";
import Logs from "./Logs";
import { Text } from "troika-three-text";
import SSText from "./SSText";
import LevelProgressBar from "./LevelProgressBar";
import party from "./party.min.js";
import AnimatedTexture from "./AnimatedTexture";
import Config from "./Config.js";
import SSLine from "./SSLine";

const confetti = require("canvas-confetti");
const cloneDeep = require("clone-deep");

let skipLetsPlay = false;

class SSScene {
  constructor(levelNum, jsondata, loadingManager, scene, camera, renderer, backgroundSound, listner, callback, sdk) {
    renderer.domElement.style.touchAction = "none";

    this.callback = callback;
    this.jsondata = jsondata;

    this.imagePaths = [];
    for (let i = 0; i < jsondata["objs"].length; ++i) {
      this.imagePaths.push(jsondata["objs"][i]["image_path"]);
    }

    this.imageAssets = [];

    this.levelNum = levelNum;
    this.sdk = sdk;
    this.loadingManager = loadingManager;
    this.camera = camera;
    this.scene = scene;
    this.renderer = renderer;
    this.backgroundSound = backgroundSound;
    this.listener = listner;
    this.assetManager = new AssetManager();
    this.raycaster = new THREE.Raycaster();
    this.mouse = new THREE.Vector2();
    this.mascot = null;
    this.lerpingObjs = [];
    this.blinkingObjs = [];
    this.clock = new THREE.Clock();

    this.playableCheckPoints = [];
    this.loadAllPlayableCheckPoints();

    this.time = 0;
    this.timer = new Timer(this);

    this.enableEvents = true;

    this.lines = [];

    // This is to identify playmode is because of any wrongness and play only which are wrong.
    this.replay_only_wrong = false;

    this.logs = new Logs(sdk);
    this.hintDialogEnabled = false;
    this.audioPlayers = [];
    this.audioLoader = new THREE.AudioLoader();

    this.interaction_audio_anytime = [];

    this.load(loadingManager);

    this.positionalObjs = [];
    this.enabledImages = [];
    this.enabledAnimatedImages = [];
    this.enabledTexts = [];
    this.progressBars = [];
    this.screens = [];
    this.steps = [];
    this.stepIndex = 0;
    this.currentAudioPath = null;
    this.skipWrongnessTrackerReset = false;

    this.dictionaries = [];

    this.inputs = [];
    this.inputWait = null;
    this.currentInput = -1;
    this.enableInputClue = null;

    this.stepDetails = null;

    this.clickToExit = false;

    this.drags = [];
    this.dragDetails = null;
    this.currentDrag = null;
    this.timeToDragCompletionAnnoucement = 0;
    this.dragDisplacement = {
      x: 0,
      y: 0,
    };

    this.interaction_click_actions = null;
    this.completion_action_ids = null;
    this.userInteractionDisableTime = 0;
    this.userInteractionDisableDuration = 0;
    this.interaction_overlay = null;
    this.interaction_overlayStartTime = 0;
    this.interaction_overlayDuration = 0;

    this.toggles = [];
    this.toggleResult;
    this.toggleClearStart = -1;
    this.toggleClearDuration = -1;

    this.wrongnessTracker = null;
    this.wrongnessTrackerLocal = null;

    this.verifyPositions = null;

    this.dialogs = [];
    this.dialog = null;

    this.menu = null;

    this.dotted = null;
    this.dottedAnimatedTexture = null;
    this.speech_dotted_index = 0;

    this.mouseDown = false;
    this.pointerId = 0;

    this.jumpToStep = 0;
    this.audioInstruction = null;

    this.skipOnHint = false;
    this.hintFromStep = -1;
    this.wrongnessFromContinue = false;

    this.noInteractions = 0.0;

    this.removeImageOnTimerIds = [];

    this.audio_on_inactivity = null;
    this.audio_on_inactivity_start = 0;

    // for (let i = 0; i < this.jsondata["screens"].length; ++i) {
    //     var screen = new SSScreen(this.jsondata["screens"][i], loadingManager, scene, camera, renderer, sound, backgroundSound, this, sdk);
    //     this.screens.push(screen);
    // }

    // let line = new SSLine(this.scene);
    // this.lines.push(line);
  }

  dispose() {
    document.removeEventListener("pointerup", this.onDocumentMouseDown, false);
    document.removeEventListener("pointermove", this.onDocumentMouseMove, false);
    document.removeEventListener("pointerup", this.onDocumentMouseUp, false);
    document.removeEventListener("pointerout", this.onDocumentMouseUp, false);

    // Dispose only what is created here
    this.assetManager.dispose();
    for (let i = 0; i < this.audioPlayers.length; ++i) this.audioPlayers[i].dispose();

    for (let i = 0; i < this.imageAssets.length; ++i) {
      this.scene.remove(this.imageAssets[i]);
    }

    this.removeAllImages();

    for (let i = 0; i < this.enabledTexts.length; ++i) {
      this.scene.remove(this.enabledTexts[i].text);
    }
  }

  stopAllAudio = () => {
    for (let i = 0; i < this.audioPlayers.length; ++i) this.audioPlayers[i].dispose();

    this.audioPlayers = [];
  };

  removeAsset = (image) => {
    const index = this.imageAssets.indexOf(image);
    if (index > -1) {
      this.imageAssets.splice(index, 1);
    }
  };

  load = async (loadingManager) => {
    console.log("Level loading...");

    // Load all assets. Textures, sounds, models required
    await this.assetManager.loadTextures(loadingManager, this.imagePaths);
    // Create scene
    this.createScene();
    loadingManager.onSceneLoaded();
  };

  setEnableEvents = (bEnable) => {
    this.enableEvents = bEnable;
  };

  updateForResize = () => {
    for (let i = 0; i < this.screens.length; ++i) {
      this.screens[i].updateForResize();
    }

    for (let i = 0; i < this.enabledImages.length; ++i) {
      this.enabledImages[i].updateForResize();
    }

    for (let i = 0; i < this.enabledAnimatedImages.length; ++i) {
      this.enabledAnimatedImages[i].updateForResize();
    }

    for (let i = 0; i < this.enabledTexts.length; ++i) {
      this.enabledTexts[i].updateForResize();
    }
  };

  getTexture = (image, name, transparent, x, y, z, vec = null, wrappingS = null, wrappingT = null) => {
    var asset = Utils.getTexture(image, name, transparent, x, y, z, vec, wrappingS, wrappingT);
    this.imageAssets.push(asset);
    return asset;
  };

  getTexture2 = (image, name, transparent, xref, x, yref, y, z, vec = null, wrappingS = null, wrappingT = null) => {
    let texture = this.getTexture(image, name, transparent, x, y, z, vec, wrappingS, wrappingT);
    return new Texture(texture, xref, x, yref, y);
  };

  getTextureWithId = (id) => {
    for (let i = 0; i < this.positionalObjs.length; ++i) {
      if (this.positionalObjs[i].texture.name == id) {
        return this.positionalObjs[i];
      }
    }
    return null;
  };

  addStep = (step) => {
    let step_num = step["details"]["step_num"];

    for (let i = 0; i < this.steps.length; ++i) {
      if (this.steps[i]["details"]["step_num"] > step_num) {
        this.steps.splice(i, 0, step);
        return;
      }
    }
    this.steps.push(step);
  };

  fetchAndStep = (jsonkey, type) => {
    if (jsonkey in this.jsondata) {
      for (let i = 0; i < this.jsondata[jsonkey].length; ++i) {
        let step = {
          type: type,
          details: this.jsondata[jsonkey][i],
        };
        this.addStep(step);
      }
    }
  };

  createScene = () => {
    // Add Game Objects.
    for (let i = 0; i < this.jsondata["objs"].length; ++i) {
      let obj = this.jsondata["objs"][i];
      let position = obj["position"];
      let scale = obj["scale"];
      if (obj["id"] == "speech_dotted") {
        this.speech_dotted_index = i;
      }
      let image = this.getTexture2(this.assetManager.textures[i], obj["id"], true, obj["xref"], position.x, obj["yref"], position.y, position.z);
      image.setScale(scale.x, scale.y);

      this.positionalObjs.push(image);
    }

    this.fetchAndStep("enableSteps", "enable");
    this.fetchAndStep("waitSteps", "wait");
    this.fetchAndStep("disableSteps", "disable");
    this.fetchAndStep("translateAnimationSteps", "translate_animation");
    this.fetchAndStep("audioSteps", "audio");
    this.fetchAndStep("stopAudioInstructionSteps", "stop_audio_instruction");
    this.fetchAndStep("dragSteps", "drag");
    this.fetchAndStep("menuSteps", "menu");
    this.fetchAndStep("enableLayoutSteps", "enable_layout");
    this.fetchAndStep("interactionClickSteps", "interaction_click");
    this.fetchAndStep("dialogSteps", "dialog");
    this.fetchAndStep("toggleSteps", "toggle");
    this.fetchAndStep("toggleResultSteps", "toggle_result");
    this.fetchAndStep("wrongnessTrackerSteps", "wrongness_tracker");
    this.fetchAndStep("jumpToStateOnWrongResultSteps", "jump_to_state_on_wrong_result");
    this.fetchAndStep("verifyPositionsSteps", "verify_positions");
    this.fetchAndStep("jumpToSteps", "jump_to_step");
    this.fetchAndStep("enableDialogSteps", "enable_dialog");
    this.fetchAndStep("audioBackgroundSteps", "audio_background");
    this.fetchAndStep("logSteps", "log_step");
    this.fetchAndStep("textSteps", "text");
    this.fetchAndStep("progressBarSteps", "progress_bar");
    this.fetchAndStep("progressBarFillSteps", "progress_bar_fill");
    this.fetchAndStep("speechBubbleAnimationSteps", "speech_bubble");
    this.fetchAndStep("removeSpeechBubbleAnimationSteps", "remove_speech_bubble");
    this.fetchAndStep("inputSteps", "input");
    this.fetchAndStep("inputWaitSteps", "input_wait");
    this.fetchAndStep("disableInputSteps", "disable_input");
    this.fetchAndStep("removeAllInputSteps", "remove_all_input");
    this.fetchAndStep("dictionarySteps", "dictionary");
    this.fetchAndStep("enableInputClueSteps", "enable_input_clue");
    this.fetchAndStep("resetGlobalWrongnessTrackerSteps", "reset_global_wrongness_tracker");
    this.fetchAndStep("interactionAudioAnytimeSteps", "interaction_audio_anytime");
    this.fetchAndStep("stopInteractionAudioAnytimeSteps", "stop_interaction_audio_anytime");
    this.fetchAndStep("audioOnInactivitySteps", "audio_on_inactivity");
    this.fetchAndStep("audioOnInactivityStopSteps", "audio_on_inactivity_stop");

    let skippedLevel = localStorage.getItem(Config.getUniqueKeyFor("skipped_level"));
    if (skippedLevel) {
      localStorage.removeItem(Config.getUniqueKeyFor("skipped_level"));

      let previous_jump_stepnum = localStorage.getItem(Config.getUniqueKeyFor("previous_step_completed_" + this.levelNum));
      console.log("previous_jump_stepnum: ", previous_jump_stepnum);
      if (previous_jump_stepnum) {
        console.log("##################################### Skipped 3");
        // load previously saved results for the level
        this.loadAllPlayableCheckPointsResults();

        previous_jump_stepnum = parseInt(previous_jump_stepnum);
        this.stepIndex = previous_jump_stepnum;
        console.log(this.stepIndex);
        this.goToCheckPoint();
      } else {
        console.log("##################################### Skipped 4");
        this.stepIndex = 0;
        this.loadStep(this.stepIndex);
      }
    } else if (this.logs.IsLevelCompleted(this.levelNum)) {
      console.log("##################################### Skipped 5");
      this.stepIndex = this.jsondata["settings"]["gameplay_stepnum"];
      console.log(this.stepIndex);
      if (this.stepIndex == -1) {
        this.stepIndex = 0;
        this.loadStep(this.stepIndex);
      } else {
        this.goToCheckPoint();
      }
    } else {
      console.log("##################################### Skipped 6");
      this.stepIndex = 0;
      this.loadStep(this.stepIndex);
    }

    this.updateForResize();

    document.addEventListener("pointerdown", this.onDocumentMouseDown, false);
    document.addEventListener("pointermove", this.onDocumentMouseMove, false);
    document.addEventListener("pointerup", this.onDocumentMouseUp, false);
    document.addEventListener("pointerout", this.onDocumentMouseUp, false);

    this.setEnableEvents(true);

    if (this.levelNum != 0) this.sdk.setPlaycount(this.levelNum);
  };

  update = () => {
    const delta = this.clock.getDelta();
    this.time += delta;

    this.timer.update();

    if (this.dottedAnimatedTexture) this.dottedAnimatedTexture.update();

    let i = 0;
    while (i < this.lerpingObjs.length) {
      this.lerpingObjs[i].doLerp(delta);
      i += 1;
    }

    i = 0;
    while (i < this.blinkingObjs.length) {
      this.blinkingObjs[i].doBlinking(delta);
      i += 1;
    }

    if (this.interaction_overlay != null && this.interaction_overlayDuration > 0) {
      if (this.time - this.interaction_overlayStartTime >= this.interaction_overlayDuration) {
        for (let k = 0; k < this.enabledImages.length; ++k) {
          if (this.enabledImages[k].texture.name == this.interaction_overlay.texture.name) {
            this.scene.remove(this.enabledImages[k].texture);
            this.enabledImages.splice(k, 1);
            break;
          }
        }

        this.interaction_overlay = null;
        this.interaction_overlayDuration = 0;
      }
    }

    if (this.toggleClearDuration > 0 && this.time - this.toggleClearStart >= this.toggleClearDuration) {
      this.toggleClearDuration = -1;
      this.toggleClearStart = -1;
      this.resetAlltoggles();
    }

    if (this.drags.length > 0 && this.dragDetails) {
      // Its interaction drag step is active
      // check for this.dragDetails
      if (this.timeToDragCompletionAnnoucement != 0 && this.time - this.timeToDragCompletionAnnoucement >= 0) {
        this.timeToDragCompletionAnnoucement = this.time + this.dragDetails["repeatOnCompleteInterval"];
        this.playAudioPath(this.dragDetails["repeatOnCompleteAudioClip"]);
        if (this.dragDetails["hightlightClickButton"].length > 0) {
          let clickimage = this.dragDetails["click_action"];
          this.removeImage(this.dragDetails["hightlightClickButton"]);
          this.addImage(this.dragDetails["hightlightClickButton"], clickimage.position.x, clickimage.position.y, clickimage.position.xref, clickimage.position.yref, clickimage.scale.x, clickimage.scale.y, clickimage["id"]);
        }
      }
    }

    this.cleaupAudioEnded();

    // InputWait actions
    if (this.inputWait != null) {
      let bChanged = this.currentInput == -1 ? true : false;
      let numberOfInputsFilled = 0;
      this.currentInput = this.inputWait.inputids.length - 1;
      for (let i = 0; i < this.inputWait.inputids.length; ++i) {
        let input = this.getInput(this.inputWait.inputids[i]);
        //console.log(input.dynamicKey);
        if (input.dynamicKey.length == 0) {
          if (this.currentInput != i) {
            bChanged = true;
            for (let k = 0; k < input.enables.length; ++k) this.removeImage(input.enables[k].id);
          }
          this.currentInput = i;
          break;
        } else {
          numberOfInputsFilled += 1;
          for (let k = 0; k < input.enables.length; ++k) this.removeImage(input.enables[k].id);

          if (this.enableInputClue) {
            let clueHighlight = this.enableInputClue.inputClueHighlightIds[i];
            this.removeImage(clueHighlight);
          }
        }
      }

      //console.log(this.currentInput);

      // Enable delete if even one input is filled
      let deleteImage = this.getTextureWithId(this.inputWait.deleteid);
      if (numberOfInputsFilled == 0) this.removeImage(this.inputWait.deleteid);
      else this.addImage(this.inputWait.deleteid, deleteImage.xdisp, deleteImage.ydisp, deleteImage.xref, deleteImage.yref, 1, 1, null);

      // Enable exit if all inputs are filled
      let exitImage = this.getTextureWithId(this.inputWait.exitid);
      if (numberOfInputsFilled == this.inputWait.inputids.length) {
        this.addImage(this.inputWait.exitid, exitImage.xdisp, exitImage.ydisp, exitImage.xref, exitImage.yref, 1, 1, null);
        this.timer.addTimeOut(this.inputWait.enable_highlight_after_complete, "add_input_highlight_after_complete");
      } else {
        this.removeImage(this.inputWait.exitid);
        this.removeImage(this.inputWait.exit_highlight_id);
        this.timer.removeTimeOutAll("add_input_highlight_after_complete");
      }

      let input = this.getInput(this.inputWait.inputids[this.currentInput]);
      // Enable all enables of current input.
      for (let k = 0; k < input.enables.length; ++k) {
        if (bChanged || !this.isImageEnabled(input.enables[k].id)) {
          this.addImage(input.enables[k].id, input.enables[k].position.x, input.enables[k].position.y, 0, 0, 1, 1, input.enables[k].parentid);
        }
      }

      if (this.enableInputClue) {
        let clueFor = this.enableInputClue.inputClueIds[this.currentInput];
        let clueHighlight = this.enableInputClue.inputClueHighlightIds[this.currentInput];
        this.removeImage(clueHighlight);
        this.addImage(clueHighlight, 0, 0, 0, 0, 1, 1, clueFor);
      }
    }

    if (this.audio_on_inactivity && !this.dialog) {
      if (this.audio_on_inactivity_start + this.audio_on_inactivity["duration"] < this.time) {
        this.playAudioPath(this.audio_on_inactivity["clip"], true);

        this.audio_on_inactivity_start = this.time;
      }
    }

    // this.drawLine(this.time);
  };

  // drawLine = (p) => {
  //     this.lines[0].points = [];

  //     this.lines[0].points.push(-100, 0, -10);
  //     this.lines[0].points.push(0, p*10, -10);
  //     this.lines[0].points.push(100, 0, -10);

  //     this.lines[0].draw();
  // }

  isImageEnabled = (id) => {
    for (let i = 0; i < this.enabledImages.length; ++i) {
      if (this.enabledImages[i].texture.name == id) return true;
    }
    return false;
  };

  getInput = (key) => {
    for (let i = 0; i < this.inputs.length; ++i) {
      if (this.inputs[i].uniqueid == key) return this.inputs[i];
    }

    return null;
  };

  setEnableEvents = (bEnable) => {
    this.enableEvents = bEnable;
  };

  getDictionary = (dictionaryKey, key) => {
    let dictionary = null;
    for (let i = 0; i < this.dictionaries.length; ++i) {
      if (this.dictionaries[i].key == dictionaryKey) {
        dictionary = this.dictionaries[i];
        break;
      }
    }
    if (dictionary == null) dictionary = this.dictionaries[0];
    for (let i = 0; i < dictionary.elements.length; ++i) {
      if (dictionary.elements[i].key == key) {
        return dictionary.elements[i].values;
      }
    }

    return [];
  };

  goToNextWronggedStep = () => {
    console.log("****************** goToNextWronggedStep");

    this.stepIndex += 1;
    console.log("this.stepIndex: ", this.stepIndex);
    let playNextAvailable = false;
    for (let i = 0; i < this.playableCheckPoints.length; ++i) {
      let playable = this.playableCheckPoints[i];
      // console.log(playable);
      // this will make sure, next step is jumped.
      if (this.stepIndex >= this.playableCheckPoints[i].start_step_num && this.stepIndex <= this.playableCheckPoints[i].end_step_num) {
        if (this.playableCheckPoints[i].wrongnessTracker == 0) {
          playNextAvailable = true;
          continue;
        }

        //playable["result"] = 0;
        // playable["wrongnessTracker"] = 0;

        playable["resetWrongnessTracker"] = 1;

        this.stepIndex = this.playableCheckPoints[i].start_step_num;
        this.loadStep(this.stepIndex);
        return;
      }

      // If didnt get next step them jump into next available wrongness step
      if (playNextAvailable && this.playableCheckPoints[i].wrongnessTracker != 0) {
        playable["result"] = 0;
        playable["resetWrongnessTracker"] = 1;
        // playable["wrongnessTracker"] = 0;

        this.stepIndex = this.playableCheckPoints[i].start_step_num;
        this.loadStep(this.stepIndex);

        return;
      }
    }

    // All are completed. No more check points left to complete. // Search for "jump_to_state_on_wrong_result"
    this.dumpAllPlayableCheckPointsResults();
    let lastPlayableCheckPoint = this.playableCheckPoints[this.playableCheckPoints.length - 1];
    for (let i = lastPlayableCheckPoint.start_step_num; i < this.steps.length; ++i) {
      let step = this.steps[i];
      if (step["type"] == "jump_to_state_on_wrong_result") {
        this.stepIndex = i;
        this.loadStep(this.stepIndex);

        return;
      }
    }

    console.error("Control should not come here. Something wrong. please check");
  };

  goToNextStep = () => {
    // console.log("goToNextStep 1");
    if (this.replay_only_wrong) {
      // console.log("goToNextStep 2: this.stepIndex: ", this.stepIndex);
      // Skip last check point otherwise it will loop according to logic written
      for (let i = 0; i < this.playableCheckPoints.length - 1; ++i) {
        let playable = this.playableCheckPoints[i];
        if (playable.end_step_num == this.stepIndex) {
          // console.log("goToNextStep 3");

          this.goToNextWronggedStep();
          return;
        }
      }
    }
    // console.log("goToNextStep 4");

    this.stepIndex += 1;
    this.loadStep(this.stepIndex);
  };

  getTextureWithIdForInput = (id, index) => {
    for (let i = 0; i < this.inputs.length; ++i) {
      //console.log(this.inputs[i]);
      if (this.inputs[i].uniqueid == id) {
        if (this.inputs[i].dynamicKey.length > 0) {
          //console.log(`Removing ${this.inputs[i].dynamicKey}`);
          this.removeImage(this.inputs[i].dynamicKey);
          if (index != -1) {
            let ids = this.getDictionary(this.inputs[i].key, this.inputs[i].dynamicInput);
            //List<string> ids = GetIdsFromDictionaryForKey(this.inputs[i].dynamicInput);
            if (ids.length > 0) this.inputs[i].dynamicKey = ids[index];
            else this.inputs[i].dynamicInput = "";
          }
          let image = this.getTextureWithId(this.inputs[i].dynamicKey);
          //image = GetImageForID(_inputs[i].dynamicKey);
          //image.gameObject.SetActive(true);
          //console.log(image);
          return image;
        }
        break;
      }
    }
    return null;
  };

  enableImages = (images) => {
    for (let i = 0; i < images.length; ++i) {
      let imageDetails = images[i];
      //console.log("imageDetails[id]: " + imageDetails["id"]);
      let image = this.getTextureWithId(imageDetails["id"]);
      if (image == null) {
        console.log("----------- Enable images for Input: ", imageDetails["id"]);
        // It might be input. Look at input
        image = this.getTextureWithIdForInput(imageDetails["id"], imageDetails["indexInDictionary"]);
        if (image == null) {
          console.error("error in enabling image from input", 5);
        }
      }
      let pos = new THREE.Vector3(0, 0, image.texture.position.z);
      pos.x = imageDetails["position"].x;
      pos.y = imageDetails["position"].y;
      let xref = imageDetails["xref"];
      let yref = imageDetails["yref"];
      if (imageDetails["parentid"].length > 0) {
        let parentImage = this.getTextureWithId(imageDetails["parentid"]);

        if (parentImage.xref != 0 || parentImage.yref != 0) {
          pos.x += parentImage.xdisp;
          pos.y += parentImage.ydisp;
        } else {
          pos.x += parentImage.texture.position.x;
          pos.y += parentImage.texture.position.y;
        }

        xref = parentImage.xref;
        yref = parentImage.yref;
      }

      image.texture.material.opacity = imageDetails["alpha"];

      image.setNewPosition(xref, pos.x, yref, pos.y);
      image.setScale(imageDetails["scale"].x, imageDetails["scale"].y);
      this.scene.add(image.texture);
      this.enabledImages.push(image);
    }
  };

  enableButtons = (buttons) => {
    for (let i = 0; i < buttons.length; ++i) {
      let details = buttons[i];

      if (details["toggle"] == 0) {
        this.addImage(details["normal_id"], details["position"].x, details["position"].y, details["xref"], details["yref"], details["scale"].x, details["scale"].y, details["parent_id"]);
      } else {
        this.addImage(details["state1_id"], details["position"].x, details["position"].y, details["xref"], details["yref"], details["scale"].x, details["scale"].y, details["parent_id"]);
        this.addImage(details["state2_id"], details["position"].x, details["position"].y, details["xref"], details["yref"], details["scale"].x, details["scale"].y, details["parent_id"]);
        this.removeImage(details["state2_id"]);

        if (details["action"] == "music_setting") {
          console.log("details[action]  2");
          let val = localStorage.getItem(Config.getUniqueKeyFor("background_music"));
          val = val == null ? 1 : parseInt(val);
          if (val == 1) {
            this.removeImage(details["state2_id"]);
            this.addImage(details["state1_id"], details["position"].x, details["position"].y, details["xref"], details["yref"], details["scale"].x, details["scale"].y, details["parent_id"]);
          } else {
            this.removeImage(details["state1_id"]);
            this.addImage(details["state2_id"], details["position"].x, details["position"].y, details["xref"], details["yref"], details["scale"].x, details["scale"].y, details["parent_id"]);
          }
        }
      }
    }
  };

  enableStep = (stepDetails) => {
    console.log("********** enable step");
    let images = stepDetails["images"];
    this.enableImages(images);
  };

  disableStep = (stepDetails) => {
    console.log("********** disable step");
    let images = stepDetails["images"];
    for (let i = 0; i < images.length; ++i) {
      let id = images[i];
      // console.log(id);
      let found = false;
      for (let k = 0; k < this.enabledImages.length; ++k) {
        if (this.enabledImages[k].texture.name == id) {
          this.scene.remove(this.enabledImages[k].texture);
          this.enabledImages.splice(k, 1);
          found = true;
          break;
        }
      }

      if (!found) {
        // console.log("Didnt find.");
        for (let i = 0; i < this.inputs.length; ++i) {
          if (this.inputs[i].uniqueid == id) {
            // console.log(`Disabling input image: ${this.inputs[i].dynamicKey}`);
            for (let k = 0; k < this.enabledImages.length; ++k) {
              if (this.enabledImages[k].texture.name == this.inputs[i].dynamicKey) {
                // console.log(`Disabling Now `);
                this.scene.remove(this.enabledImages[k].texture);
                this.enabledImages.splice(k, 1);
                found = true;
                break;
              }
            }
            break;
          }
        }
      }
    }
  };

  waitStep = (stepDetails) => {
    console.log("********** wait step");
    let duration = stepDetails["duration"];
    let clickToExit = stepDetails["clickToExit"];
    if (duration == -1 && clickToExit == true) this.clickToExit = true;
    else this.timer.addTimeOut(duration, "nextstep");
  };

  translationStep = (stepDetails) => {
    console.log("********** translate_animation step");
    let translations = stepDetails["translations"];
    for (let i = 0; i < translations.length; ++i) {
      let translation = translations[i];
      let image = this.getTextureWithId(translation["id"]);
      let pos1 = image.texture.position;
      let pos2 = new THREE.Vector3(translation["position"].x, translation["position"].y, image.texture.position.z);
      image["dest_pos_z"] = pos2.z;
      let newPos1 = cloneDeep(pos1);
      let newPos2 = cloneDeep(pos2);
      newPos1.z += 30;
      newPos2.z += 30;
      if (newPos1.z > 0) newPos1.z = 0;
      if (newPos2.z > 0) newPos2.z = 0;
      image.texture.position.z = newPos1.z;
      image.setLerp(newPos1, newPos2, translation["duration"], false, false, this, translation["duration"]);
      this.lerpingObjs.push(image);
    }
  };

  audioStep = (stepDetails) => {
    console.log("********** audio step");
    let audioPath = stepDetails["audio_path"];
    let volume = stepDetails["volume"];
    if (stepDetails["audio_instruction"] == 1) {
      this.audioInstruction = audioPath;
      if (Config.audioInstructionOnlyOnReplay) return;
    }
    this.playAudioPath(audioPath);
  };

  audioBackgroundStep = (stepDetails) => {
    console.log("********** audio background");
    let audioPaths = stepDetails["audio_paths"];
    let index = Math.floor(Math.random() * audioPaths.length);
    this.playBackgroundMusic(true, audioPaths[index], stepDetails["volume"]);
  };

  dialogStep = (stepDetails) => {
    console.log("********** dialog step");
    this.dialogs.push(cloneDeep(stepDetails));
  };

  menuStep = (stepDetails) => {
    console.log("********** menu step");
    this.enableImages(stepDetails["images"]);
    this.enableButtons(stepDetails["buttons"]);
    this.menu = cloneDeep(stepDetails);

    this.validateMenuButtons();
  };

  validateMenuButtons = () => {
    let menuAdded = false;
    for (let i = 0; i < this.enabledImages.length; ++i) {
      if (this.enabledImages[i].texture.name == "button_home") {
        menuAdded = true;
      }
    }

    if (menuAdded == false) return;

    if (this.jsondata["settings"]["gameplay_stepnum"] == -1) {
      if (this.levelNum == 0 && this.logs.IsLevelCompleted(this.levelNum)) {
      } else {
        this.removeImage("button_skip");
      }
      return;
    }

    if (this.stepIndex < this.jsondata["settings"]["gameplay_stepnum"]) {
      this.removeImage("button_back");
      this.removeImage("button_hint");
      this.removeImage("button_back_inactive");
      this.removeImage("button_hint_inactive");

      let backimageinactive = this.getTextureWithId("button_back_inactive");
      //console.log(backimageinactive);
      this.addImage("button_back_inactive", backimageinactive.xdisp, backimageinactive.ydisp, backimageinactive.xref, backimageinactive.yref, 1, 1, null);

      let hintimageinactive = this.getTextureWithId("button_hint_inactive");
      this.addImage("button_hint_inactive", hintimageinactive.xdisp, hintimageinactive.ydisp, hintimageinactive.xref, hintimageinactive.yref, 1, 1, null);

      if (this.skipOnHint) {
        this.skipWrongnessTrackerReset = true;
        let skipimage = this.getTextureWithId("button_skip");
        this.addImage("button_skip", skipimage.xdisp, skipimage.ydisp, skipimage.xref, skipimage.yref, 1, 1, null);
      } else {
        this.removeImage("button_skip");
      }
    } else {
      let backimage = this.getTextureWithId("button_back");
      this.addImage("button_back", backimage.xdisp, backimage.ydisp, backimage.xref, backimage.yref, 1, 1, null);

      let hintimage = this.getTextureWithId("button_hint");
      this.addImage("button_hint", hintimage.xdisp, hintimage.ydisp, hintimage.xref, hintimage.yref, 1, 1, null);

      this.removeImage("button_back_inactive");
      this.removeImage("button_hint_inactive");

      if (this.logs.IsLevelCompleted(this.levelNum)) {
        let skipimage = this.getTextureWithId("button_skip");
        this.addImage("button_skip", skipimage.xdisp, skipimage.ydisp, skipimage.xref, skipimage.yref, 1, 1, null);
      } else {
        this.removeImage("button_skip");
      }
    }
  };

  textStep = (stepDetails) => {
    let pos = new THREE.Vector3(0, 0, stepDetails["position"].z);
    pos.x = stepDetails["position"].x;
    pos.y = stepDetails["position"].y;
    let xref = stepDetails["xref"];
    let yref = stepDetails["yref"];
    if (stepDetails["parentid"].length > 0) {
      let parentImage = this.getTextureWithId(stepDetails["parentid"]);

      if (parentImage.xref != 0 || parentImage.yref != 0) {
        pos.x += parentImage.xdisp;
        pos.y += parentImage.ydisp;
      } else {
        pos.x += parentImage.texture.position.x;
        pos.y += parentImage.texture.position.y;
      }

      xref = parentImage.xref;
      yref = parentImage.yref;
    }

    let text = new Text();
    this.scene.add(text);
    if (stepDetails["alignment"] == 0) text.anchorX = "center";
    else if (stepDetails["alignment"] == 1) text.anchorX = "right";
    else if (stepDetails["alignment"] == -1) text.anchorX = "left";

    console.log(pos);
    text.fontSize = stepDetails["fontSize"];
    text.position.z = pos.z;
    text.color = 0xffffff;

    let sstext = new SSText(text, xref, pos.x, yref, pos.y, stepDetails["textid"]);
    sstext.setNewPosition(xref, pos.x, yref, pos.y);
    this.enabledTexts.push(sstext);

    if (stepDetails["textid"] == "rewards") {
      this.updateRewardsText();
    }
  };

  speechBubbleStep = (stepDetails) => {
    let dottedImage = null;
    for (let i = 0; i < this.enabledAnimatedImages.length; ++i) {
      if (this.enabledAnimatedImages[i].texture.name == "talk_bubble_dots") {
        dottedImage = this.enabledAnimatedImages[i];
        break;
      }
    }

    if (dottedImage == null) {
      // For animated Texture we have implement on own
      const mat = new THREE.MeshBasicMaterial({
        map: Texture.get2DTexture(this.assetManager.textures[this.speech_dotted_index]),
      });
      mat.transparent = true;

      let dotted = new THREE.Mesh(new THREE.PlaneGeometry(40, 40), mat);
      dotted.name = "talk_bubble_dots";
      dotted.position.x = stepDetails["position"].x;
      dotted.position.y = stepDetails["position"].y;
      dotted.position.z = stepDetails["position"].z;

      this.imageAssets.push(dotted);
      dottedImage = new Texture(dotted, stepDetails["xref"], stepDetails["position"].x, stepDetails["yref"], stepDetails["position"].y);
      this.enabledAnimatedImages.push(dottedImage);

      this.positionalObjs.push(dottedImage);
      this.dottedAnimatedTexture = new AnimatedTexture(this.assetManager.textures[this.speech_dotted_index], 10, 3, 28, 2);
    }

    this.scene.add(dottedImage.texture);
    dottedImage.setNewPosition(stepDetails["xref"], stepDetails["position"].x, stepDetails["yref"], stepDetails["position"].y);
  };

  removeSpeechBubbleStep = (stepDetails) => {
    for (let i = 0; i < this.enabledAnimatedImages.length; ++i) {
      if (this.enabledAnimatedImages[i].texture.name == "talk_bubble_dots") {
        this.scene.remove(this.enabledAnimatedImages[i].texture);
        break;
      }
    }
  };

  inputStep = (stepDetails) => {
    let details = cloneDeep(stepDetails);
    let bAlreadyPresent = false;
    // Check if already present, then dont add it
    for (let i = 0; i < this.inputs.length; ++i) {
      if (this.inputs[i].uniqueid == details.uniqueid) {
        bAlreadyPresent = true;
        details = this.inputs[i];
        break;
      }
    }

    details["dynamicInput"] = "";
    details["dynamicKey"] = "";

    if (!bAlreadyPresent) this.inputs.push(details);
  };

  inputWaitStep = (stepDetails) => {
    this.timer.removeTimeOutAll("add_input_highlight_after_complete");
    this.inputWait = stepDetails;
    this.currentInput = -1;
  };

  disableInputStep = (stepDetails) => {
    for (let i = 0; i < stepDetails.inputids.length; ++i) {
      for (let k = 0; k < this.inputs.length; ++k) {
        if (this.inputs[k].uniqueid == stepDetails.inputids[i]) {
          this.removeImage(this.inputs[k].dynamicKey);
          this.inputs[k].dynamicKey = "";
          this.inputs[k].dynamicInput = "";
          this.inputs.splice(k, 1);
          break;
        }
      }
    }
  };

  removeAllInputStep = (stepDetails) => {
    while (this.inputs.length > 0) {
      let input = this.inputs[0];
      this.disableInputStep(input);
      this.inputs.splice(0, 1);
    }
    this.inputs = [];
  };

  dictionaryStep = (stepDetails) => {
    console.log("--- dictionaryStep --- ");
    this.dictionaries.push(stepDetails);
  };

  progressBarStep = (stepDetails) => {
    let position = stepDetails["position"];
    let progressBar = new LevelProgressBar(this.scene, position.x, position.y, position.z, stepDetails["id"]);

    let storedProgress = localStorage.getItem(Config.getUniqueKeyFor(progressBar.id));
    storedProgress = storedProgress != null ? storedProgress : 0;
    storedProgress = parseFloat(storedProgress);
    progressBar.setProgress(storedProgress);

    this.progressBars.push(progressBar);
  };

  progressBarFillStep = (stepDetails) => {
    for (let i = 0; i < this.progressBars.length; ++i) {
      if (this.progressBars[i].id == stepDetails["id"]) {
        let newProgress = stepDetails["fill"];
        let storedProgress = localStorage.getItem(Config.getUniqueKeyFor(this.progressBars[i].id));
        storedProgress = storedProgress != null ? storedProgress : 0;
        storedProgress = parseFloat(storedProgress);
        if (newProgress > storedProgress) {
          localStorage.setItem(Config.getUniqueKeyFor(this.progressBars[i].id), newProgress);
          this.progressBars[i].setProgress(newProgress);
        }
        break;
      }
    }
  };

  updateRewardsText = () => {
    for (let i = 0; i < this.enabledTexts.length; ++i) {
      if (this.enabledTexts[i].textid == "rewards") {
        if(Config.buildVersion.length > 0)
          this.enabledTexts[i].text.text = "" + this.sdk.getTotalRewardPoints() + ' : ' + Config.buildVersion;
        else
          this.enabledTexts[i].text.text = "" + this.sdk.getTotalRewardPoints();
        this.enabledTexts[i].text.sync();
        break;
      }
    }
  };

  loadStep = (index) => {
    if (this.dialog != null) return;

    console.log("loadStep: ", index);

    if (index >= this.steps.length) return;

    let step = this.steps[index];
    let stepDetails = step["details"];

    console.log("type: ", step["type"]);
    //console.log(step);

    this.validateMenuButtons();

    if (index == this.jsondata["settings"]["gameplay_stepnum"] - 1) {
      if (this.hintFromStep != -1) {
        this.hintDialogEnabled = true;
        this.enableDialog("dialog_hint");
        return;
      }
    }

    if (step["type"] == "log_step") {
      if (stepDetails["even_name"] == "presentation_start") {
        this.logs.logEventLevelStartDuration(this.levelNum);
        this.logs.logEventPresentationStartDuration(this.levelNum);
      } else if (stepDetails["even_name"] == "presentation_end") {
        this.logs.logEventPresentationEndDuration(this.levelNum);
        console.log(this.sdk);
      }
    }

    if (step["type"] == "enable") {
      this.enableStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "wait") {
      if (Config.debug1 && stepDetails["step_num"] < this.jsondata["settings"].gameplay_stepnum) {
        this.goToNextStep();
      } else if (Config.debug2 && stepDetails["step_num"] >= this.jsondata["settings"].gameplay_stepnum) {
        this.goToNextStep();
      } else {
        console.log(3);
        this.waitStep(stepDetails);
      }
      return;
    }

    if (step["type"] == "disable") {
      this.disableStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "speech_bubble") {
      this.speechBubbleStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "remove_speech_bubble") {
      this.removeSpeechBubbleStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "input") {
      this.inputStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "input_wait") {
      this.inputWaitStep(stepDetails);
      return;
    }

    if (step["type"] == "disable_input") {
      this.disableInputStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "remove_all_input") {
      this.removeAllInputStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "dictionary") {
      this.dictionaryStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "enable_input_clue") {
      this.enableInputClue = stepDetails;
      this.goToNextStep();
      return;
    }

    if (step["type"] == "progress_bar") {
      this.progressBarStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "progress_bar_fill") {
      this.progressBarFillStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "translate_animation") {
      if (Config.enable_transalate_animation) this.translationStep(stepDetails);
      else this.goToNextStep();

      return;
    }

    if (step["type"] == "audio") {
      if (Config.debug1 && stepDetails["step_num"] < this.jsondata["settings"].gameplay_stepnum) {
      } else if (Config.debug2 && stepDetails["step_num"] >= this.jsondata["settings"].gameplay_stepnum) {
      } else {
        this.audioStep(stepDetails);
      }
      this.goToNextStep();

      return;
    }

    if (step["type"] == "audio_background") {
      this.audioBackgroundStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "stop_audio_instruction") {
      this.audioInstruction = null;
      this.goToNextStep();
      return;
    }

    if (step["type"] == "drag") {
      console.log("********** drag step");
      this.dragDetails = stepDetails;
      this.timeToDragCompletionAnnoucement = 0;
      this.drags = stepDetails["drags"];
      //console.log(this.drags);

      for (let i = 0; i < this.drags.length; ++i) {
        let image = this.getTextureWithId(this.drags[i]["id"]);
        this.drags[i]["image"] = image;
        this.drags[i]["orig_position"] = new THREE.Vector3(image.texture.position.x, image.texture.position.y, image.texture.position.z);
      }

      return;
    }

    if (step["type"] == "enable_layout") {
      console.log("********** enable_layout step: ");
      console.log(stepDetails);

      let enables = stepDetails["enables"];
      let images = [];
      let totalSize = 0;
      for (let i = 0; i < enables.length; ++i) {
        if (i > 0) totalSize += stepDetails["distanceBetween"];
        let image = this.getTextureWithId(enables[i].id);
        if (image == null) {
          image = this.getTextureWithIdForInput(enables[i].id, enables[i].indexInDictionary);
          if (image == null) {
            console.error("error in enabling image from input", 5);
          }
        }

        if (stepDetails["layout"] == 1)
          // Horizontal
          totalSize += image.getWidth() * enables[i]["scale"].x;
        else totalSize += image.getHeight() * enables[i]["scale"].y;

        image.setScale(enables[i]["scale"].x, enables[i]["scale"].y);

        images.push(image);
      }

      //images = Utils.shuffleArray(images);

      let pos = cloneDeep(stepDetails["position"]);
      console.log(pos);
      if (stepDetails["layout"] == 1)
        // Horizontal
        pos.x += -totalSize / 2.0;
      else pos.y += -totalSize / 2.0;

      for (let i = 0; i < images.length; ++i) {
        console.log(i);
        if (i > 0) {
          if (stepDetails["layout"] == 1) pos.x += stepDetails["distanceBetween"];
          else pos.y += stepDetails["distanceBetween"];
        }

        if (stepDetails["layout"] == 1) pos.x += (images[i].getWidth() * images[i].texture.scale.x) / 2.0;
        else pos.y += (images[i].getHeight() * images[i].texture.scale.y) / 2.0;

        console.log(pos.x);
        images[i].setNewPosition(0, pos.x, 0, pos.y);

        if (stepDetails["layout"] == 1) pos.x += (images[i].getWidth() * images[i].texture.scale.x) / 2.0;
        else pos.y += (images[i].getHeight() * images[i].texture.scale.y) / 2.0;

        this.scene.add(images[i].texture);
        this.enabledImages.push(images[i]);
      }

      this.goToNextStep();
      return;
    }

    if (step["type"] == "interaction_click") {
      console.log("********** interaction_click step");

      this.stepDetails = stepDetails;
      this.interaction_click_actions = stepDetails["actions"];
      this.completion_action_ids = cloneDeep(stepDetails["completion_action_ids"]);
      return;
    }

    if (step["type"] == "toggle") {
      this.toggles.push(stepDetails);
      console.log(this.toggles.length);
      stepDetails["current_state"] = -1;
      this.setToggleImages(stepDetails);

      this.goToNextStep();
      return;
    }

    if (step["type"] == "toggle_result") {
      this.toggleResult = stepDetails;

      return;
    }

    if (step["type"] == "wrongness_tracker") {
      if (stepDetails["type"] == "global") {
        if (!this.wrongnessFromContinue && !this.skipWrongnessTrackerReset) {
          this.wrongnessTracker = stepDetails;
          this.wrongnessTracker["wrongResults"] = 0;
        }

        this.skipWrongnessTrackerReset = false;
        this.wrongnessFromContinue = false;

        // Load wrongness already stored in playables
        for (let k = 0; k < this.playableCheckPoints.length; ++k) {
          this.wrongnessTracker["wrongResults"] += this.playableCheckPoints[k]["wrongnessTracker"];
        }
      } else if (stepDetails["type"] == "local") {
        this.wrongnessTrackerLocal = stepDetails;
        this.wrongnessTrackerLocal["wrongResults"] = 0;
      }

      this.goToNextStep();

      return;
    }

    if (step["type"] == "reset_global_wrongness_tracker") {
      this.wrongnessTracker["wrongResults"] = 0;
      this.loadAllPlayableCheckPoints();

      this.goToNextStep();

      return;
    }

    if (step["type"] == "jump_to_state_on_wrong_result") {
      if (this.wrongnessTracker != null) {
        if (this.wrongnessTracker["wrongResults"] >= stepDetails["afterNWrongResults"]) {
          this.wrongnessTracker["wrongResults"] = 0;

          if (this.jsondata["settings"].wrongness_replay_mode == "replay_all_on_wrongness") {
            this.stepIndex = this.playableCheckPoints[0].start_step_num - 1;
          } else {
            // "replay_only_wrong_on_wrongness"
            this.replay_only_wrong = true;
            this.stepIndex = this.playableCheckPoints[0].start_step_num - 1;
            this.goToNextWronggedStep();
            return;
          }
        }
      }

      this.goToNextStep();
      return;
    }

    if (step["type"] == "verify_positions") {
      this.verifyPositions = stepDetails;
      let correctPositions = 0;
      for (let i = 0; i < stepDetails["destinations"].length; ++i) {
        let destination = stepDetails["destinations"][i];
        let image = this.getTextureWithId(destination["rightid"]);
        let pos = new THREE.Vector3(destination["position"].x, destination["position"].y, image.texture.position.z);
        if (this.isImageEnabled(destination["rightid"]) && pos.distanceTo(image.texture.position) < 5) {
          correctPositions += 1;
          for (let k = 0; k < destination["rightMarks"].length; ++k) {
            let rightMark = destination["rightMarks"][k];
            this.addImage(rightMark["id"], rightMark["position"].x, rightMark["position"].y, rightMark["xref"], rightMark["yref"], rightMark["scale"].x, rightMark["scale"].y, rightMark["parentid"]);
          }
        } else {
          for (let k = 0; k < destination["wrongMarks"].length; ++k) {
            let wrongMark = destination["wrongMarks"][k];
            this.addImage(wrongMark["id"], wrongMark["position"].x, wrongMark["position"].y, wrongMark["xref"], wrongMark["yref"], wrongMark["scale"].x, wrongMark["scale"].y, wrongMark["parentid"]);
          }
        }
      }

      if (correctPositions != stepDetails["destinations"].length) {
        if (stepDetails["wrongAudioClip"].length > 0) this.playAudioPath(stepDetails["wrongAudioClip"]);

        this.wrongnessTracker["wrongResults"] += 1;
        this.wrongnessTrackerLocal["wrongResults"] += 1;
        this.logs.logWrongAttempt(this.levelNum);

        if (this.wrongnessTrackerLocal["wrongResults"] >= stepDetails["showClueAfterNTries"]) {
          this.setForNextStepNum(stepDetails["jumpToStepOnClue"]);
        } else {
          this.setForNextStepNum(stepDetails["jumpToStepOnWrong"]);
        }
        this.saveStepResult(stepDetails["step_num"], 0);
      } else if (correctPositions == stepDetails["destinations"].length) {
        this.setForNextStepNum(stepDetails["jumpToStepOnRight"]);

        this.logs.logCorrectAttempt(this.levelNum);

        this.saveStepResult(stepDetails["step_num"], 1);
      }
      this.timer.addTimeOut(stepDetails["wait"], "nextstep");

      return;
    }

    if (step["type"] == "jump_to_step") {
      this.setForNextStepNum(stepDetails["step"]);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "dialog") {
      this.dialogStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "enable_dialog") {
      if (stepDetails["type"] == "dialog_lets_play") {
        if (!Config.showLetsPlay || skipLetsPlay) {
          if ((this.levelNum != 0 && Config.debug1 == false) || skipLetsPlay) {
            this.goToNextStep();
            return;
          }
        }
        skipLetsPlay = true;
      }

      this.enableDialog(stepDetails["type"]);
      if (stepDetails["type"] == "dialog_level_completed") {
        this.logs.logEventLevelEndDuration(this.levelNum);

        this.updateRewardsText();

        if (this.levelNum != 0) {
          setTimeout(this.showConfetti, 200);
        }
        ///this.timer.addTimeOut(0.2, "next_confetti");
      }
      return;
    }

    if (step["type"] == "menu") {
      this.menuStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "text") {
      this.textStep(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "interaction_audio_anytime") {
      this.interaction_audio_anytime.push(stepDetails);
      this.goToNextStep();
      return;
    }

    if (step["type"] == "stop_interaction_audio_anytime") {
      this.interaction_audio_anytime = [];
      this.goToNextStep();
      return;
    }

    if (step["type"] == "audio_on_inactivity") {
      this.audio_on_inactivity = stepDetails;
      this.audio_on_inactivity_start = this.time;
      this.goToNextStep();
      return;
    }

    if (step["type"] == "audio_on_inactivity_stop") {
      this.audio_on_inactivity = null;
      this.goToNextStep();
      return;
    }

    this.goToNextStep();
  };

  loadAllPlayableCheckPoints = () => {
    console.log("++++++++++++++++++++++++ loadAllPlayableCheckPoints ++++++++++++++++++++++++++ ");

    if (!this.jsondata["playableCheckPoints"]) {
      return;
    }

    this.playableCheckPoints = [];
    for (let i = 0; i < this.jsondata["playableCheckPoints"].length; ++i) {
      let playable = this.jsondata["playableCheckPoints"][i];
      playable["result"] = 0;
      playable["wrongnessTracker"] = 0;
      playable["resetWrongnessTracker"] = 0;
      this.playableCheckPoints.push(playable);
    }

    console.log(this.playableCheckPoints);
    let jsonstring = JSON.stringify(this.playableCheckPoints);
    console.log(JSON.parse(jsonstring));
  };

  loadAllPlayableCheckPointsResults = () => {
    let jsonstring = localStorage.getItem(Config.getUniqueKeyFor("playable_result_" + this.levelNum));
    if (jsonstring.length > 0) this.playableCheckPoints = JSON.parse(jsonstring);
  };

  dumpAllPlayableCheckPointsResults = () => {
    let jsonstring = JSON.stringify(this.playableCheckPoints);
    console.log(jsonstring);
    localStorage.setItem(Config.getUniqueKeyFor("playable_result_" + this.levelNum), jsonstring);
    console.log("After dump");
  };

  saveStepResult = (step_num, result) => {
    console.log("++++++++++++++++++++++++++ step_num: " + step_num + "   result:" + result);

    for (let i = 0; i < this.playableCheckPoints.length; ++i) {
      if (step_num >= this.playableCheckPoints[i].start_step_num && step_num <= this.playableCheckPoints[i].end_step_num) {
        if (this.playableCheckPoints[i].resetWrongnessTracker == 1) {
          this.playableCheckPoints[i].resetWrongnessTracker = 0;
          this.playableCheckPoints[i].wrongnessTracker = 0;
        }
        if (result == 0) {
          this.playableCheckPoints[i].wrongnessTracker += 1;
        }
        this.playableCheckPoints[i].result = result;
        break;
      }
    }
  };

  setForNextStepNum = (step_num) => {
    console.log("setForNextStepNum: ", step_num);
    for (let i = 0; i < this.steps.length; ++i) {
      if (this.steps[i]["details"]["step_num"] == step_num) {
        this.stepIndex = i - 1;
        break;
      }
    }

    console.log("this.stepIndex: ", this.stepIndex);
  };

  onScreenLoaded = (screen) => {};

  lerpEnded = (texture) => {
    const index = this.lerpingObjs.indexOf(texture);
    if (index > -1) {
      let posz = this.lerpingObjs[index]["dest_pos_z"];
      this.lerpingObjs[index].texture.position.z = posz;
      this.lerpingObjs.splice(index, 1);
    }

    if (this.lerpingObjs.length == 0) {
      this.goToNextStep();
    }
  };

  blinkingEnded = (texture) => {
    const index = this.blinkingObjs.indexOf(texture);
    if (index > -1) {
      this.blinkingObjs.splice(index, 1);
    }
  };

  resetAlltoggles = () => {
    for (let t = 0; t < this.toggles.length; ++t) {
      let toggle = this.toggles[t];
      // set current_state = -1
      toggle["current_state"] = -1;
      // set images to state 1
      this.setToggleImages(toggle);
      // remove right and wrong images
      for (let i = 0; i < toggle["right"].length; ++i) {
        this.removeImage(toggle["right"][i]);
      }
      for (let i = 0; i < toggle["wrong"].length; ++i) {
        this.removeImage(toggle["wrong"][i]);
      }
    }
  };

  setToggleImages = (toggle) => {
    for (let i = 0; i < toggle["state1"].length; ++i) {
      let imageid = toggle["state2"][i];
      if (toggle["current_state"] == 1 || toggle["current_state"] == -1) {
        let image = this.getTextureWithId(imageid);
        this.scene.add(image.texture);
        this.enabledImages.push(image);
      } else {
        // if imageid present in toggle state1 and 2 both, then dont remove it.
        let presentInBothStates = false;
        for (let k = 0; k < toggle["state2"].length; ++k) {
          if (imageid == toggle["state2"][k]) {
            presentInBothStates = true;
            break;
          }
        }
        if (!presentInBothStates) this.removeImage(imageid);
      }
    }
    for (let i = 0; i < toggle["state2"].length; ++i) {
      let imageid = toggle["state2"][i];
      if (toggle["current_state"] == 2) {
        let image = this.getTextureWithId(imageid);
        this.scene.add(image.texture);
        this.enabledImages.push(image);
      } else {
        // if imageid present in toggle state1 and 2 both, then dont remove it.
        let presentInBothStates = false;
        for (let k = 0; k < toggle["state1"].length; ++k) {
          if (imageid == toggle["state1"][k]) {
            presentInBothStates = true;
            break;
          }
        }
        if (!presentInBothStates) this.removeImage(imageid);
      }
    }
  };

  setToggleRightWrongImages = (toggle, right) => {
    for (let i = 0; i < toggle["right"].length; ++i) {
      if (right) {
        let image = this.getTextureWithId(toggle["right"][i]);
        this.scene.add(image.texture);
        this.enabledImages.push(image);
      } else {
        this.removeImage(toggle["right"][i]);
      }
    }
    for (let i = 0; i < toggle["wrong"].length; ++i) {
      if (!right) {
        let image = this.getTextureWithId(toggle["wrong"][i]);
        this.scene.add(image.texture);
        this.enabledImages.push(image);
      } else {
        this.removeImage(toggle["wrong"][i]);
      }
    }
  };

  isImageEnabled = (id) => {
    for (let i = 0; i < this.enabledImages.length; ++i) {
      if (this.enabledImages[i].texture.name == id) {
        return true;
      }
    }

    return false;
  };

  addImage = (id, x, y, xref, yref, xscale, yscale, parentid) => {
    //console.log(id);
    this.removeImage(id);
    let image = this.getTextureWithId(id);
    let pos = cloneDeep(image.texture.position);
    pos.x = x;
    pos.y = y;

    if (parentid != null && parentid.length > 0) {
      let parentImage = this.getTextureWithId(parentid);
      pos.x += parentImage.xdisp;
      pos.y += parentImage.ydisp;

      xref = parentImage.xref;
      yref = parentImage.yref;
    }

    image.setNewPosition(xref, pos.x, yref, pos.y);
    image.setScale(xscale, yscale);

    //if (this.isImageEnabled(id) == false) {

    this.scene.add(image.texture);
    this.enabledImages.push(image);
    //}
  };

  removeImage = (id) => {
    for (let k = 0; k < this.enabledImages.length; ++k) {
      if (this.enabledImages[k].texture.name == id) {
        this.scene.remove(this.enabledImages[k].texture);
        this.enabledImages.splice(k, 1);
        break;
      }
    }
  };

  enableDialog = (type) => {
    console.log("################ Enabling dialog: ", type);

    for (let i = 0; i < this.dialogs.length; ++i) {
      if (this.dialogs[i].type == type) {
        this.dialog = cloneDeep(this.dialogs[i]);
        this.enableImages(this.dialog["images"]);
        this.enableButtons(this.dialog["buttons"]);

        // kill all timers. this might be causing overlap. timer is coming back later and playing sound after mentioned wait
        this.timer.clearAll();

        for (let i = 0; i < this.dialog["audio_paths"].length; ++i) {
          this.playAudioPath(this.dialog["audio_paths"][i]);
        }

        // Disable any interactions for 0.6 seconds. To make sure that user does not keep clicking home and continue.
        // Clicking continiously on Home and Continue is causing audio overlap issue. Mobile webbrowser is not able to handle those
        // many in buffer. So we are placing a hack to disable interaction for 0.6 seconds.
        this.noInteractions = this.time + 0.6;

        break;
      }
    }
  };

  disableDialog = (type) => {
    for (let i = 0; i < this.dialogs.length; ++i) {
      if (this.dialogs[i].type == type) {
        for (let k = 0; k < this.dialogs[i]["images"].length; ++k) {
          this.removeImage(this.dialogs[i]["images"][k]["id"]);
        }
        for (let k = 0; k < this.dialogs[i]["buttons"].length; ++k) {
          this.removeImage(this.dialogs[i]["buttons"][k]["normal_id"]);
        }
        break;
      }
    }

    this.dialog = null;
  };

  removeAllImages = () => {
    while (this.enabledImages.length > 0) {
      this.scene.remove(this.enabledImages[0].texture);
      this.enabledImages.splice(0, 1);
    }
    while (this.enabledAnimatedImages.length > 0) {
      this.scene.remove(this.enabledAnimatedImages[0].texture);
      this.enabledAnimatedImages.splice(0, 1);
    }
    while (this.progressBars.length) {
      let progressBar = this.progressBars[0];
      this.progressBars.splice(0, 1);
      this.scene.remove(progressBar.obj);
    }
  };

  stopAllLerpingObjs = () => {
    for (let i = 0; i < this.lerpingObjs.length; ++i) {
      let posz = this.lerpingObjs[i]["dest_pos_z"];
      this.lerpingObjs[i].texture.position.z = posz;
    }

    this.lerpingObjs = [];
  };

  cleanAll = () => {
    this.timer.clearAll();

    this.stopAllLerpingObjs();

    // Stop all audio
    for (let i = 0; i < this.audioPlayers.length; ++i) this.audioPlayers[i].dispose();

    this.audioPlayers = [];

    // Remvoe all images and dialogs
    this.removeAllImages();
    this.currentAudioPath = null;
    this.drags = [];
    this.dragDetails = null;
    this.currentDrag = null;
    this.interaction_click_actions = null;
    this.completion_action_ids = null;
    this.userInteractionDisableTime = 0;
    this.userInteractionDisableDuration = 0;
    this.interaction_overlay = null;
    this.interaction_overlayStartTime = 0;
    this.interaction_overlayDuration = 0;
    this.toggles = [];
    this.toggleResult;
    this.toggleClearStart = -1;
    this.toggleClearDuration = -1;
    this.wrongnessTrackerLocal = null;
    this.verifyPositions = null;
    this.dialogs = [];
    this.dialog = null;
    this.menu = null;
    this.jumpToStep = 0;
    this.audioInstruction = null;
    this.inputWait = null;
    this.inputs = [];
    this.dictionaries = [];
    this.timer.removeTimeOutAll("add_input_highlight_after_complete");
  };

  removeAllAndLoadBackground = () => {
    this.cleanAll();

    // Load background steps
    let jsonkey = "backgroundSteps";
    let backgroundSteps = this.jsondata[jsonkey];

    for (let i = 0; i < backgroundSteps.length; ++i) {
      let step = this.steps[backgroundSteps[i] - 1];
      let stepDetails = step["details"];
      //console.log(step);

      if (step["type"] == "enable") {
        this.enableStep(stepDetails);
        continue;
      }

      if (step["type"] == "disable") {
        this.disableStep(stepDetails);
        continue;
      }

      if (step["type"] == "dialog") {
        this.dialogStep(stepDetails);
        continue;
      }

      if (step["type"] == "menu") {
        this.menuStep(stepDetails);
        continue;
      }

      if (step["type"] == "progress_bar") {
        this.progressBarStep(stepDetails);
        continue;
      }

      if (step["type"] == "text") {
        this.textStep(stepDetails);
        continue;
      }

      if (step["type"] == "dictionary") {
        this.dictionaryStep(stepDetails);
        continue;
      }

      if (step["type"] == "wrongness_tracker") {
        if (stepDetails["type"] == "global") {
          this.wrongnessTracker = stepDetails;
          this.wrongnessTracker["wrongResults"] = 0;

          this.skipWrongnessTrackerReset = false;
          this.wrongnessFromContinue = false;

          // Load wrongness already stored in playables
          for (let k = 0; k < this.playableCheckPoints.length; ++k) {
            this.wrongnessTracker["wrongResults"] += this.playableCheckPoints[k]["wrongnessTracker"];
          }
        }

        continue;
      }
    }
  };

  goToCheckPoint = () => {
    this.removeAllAndLoadBackground();

    // Load step for the checkpoint
    // Get the previous checkpoint
    let jsonkey = "checkPoints";
    let checkPoints = this.jsondata[jsonkey];
    let previousCheckPoint = checkPoints[0];
    if (this.jsondata["settings"].gameplay_stepnum != -1) {
      for (let i = 1; i <= checkPoints.length; ++i) {
        if (this.stepIndex >= previousCheckPoint && this.stepIndex < checkPoints[i]) {
          this.stepIndex = previousCheckPoint;
          break;
        }
        previousCheckPoint = checkPoints[i];
      }
    }

    this.stepIndex = previousCheckPoint - 1;

    if (this.stepIndex == -1) this.stepIndex = 0;
    this.loadStep(this.stepIndex);
  };

  isClickedOnDrag = (drag, intersectName) => {
    if (drag["image"].texture.name == intersectName) return true;

    for (let i = 0; i < drag["followers"].length; ++i) {
      if (drag["followers"][i] == intersectName) return true;
    }

    return false;
  };

  addWrongness = (step_num) => {
    if (this.wrongnessTracker != null) this.wrongnessTracker["wrongResults"] += 1;
    if (this.wrongnessTrackerLocal != null) this.wrongnessTrackerLocal["wrongResults"] += 1;

    this.logs.logWrongAttempt(this.levelNum);

    this.saveStepResult(step_num, 0);
  };

  addCorrectNess = (step_num) => {
    this.logs.logCorrectAttempt(this.levelNum);
    this.saveStepResult(step_num, 1);
  };

  onDocumentMouseDown = (event) => {
    console.log("-------- onDocumentMouseDown ------------");

    if (!this.enableEvents || this.time < this.noInteractions) return;

    if (this.clickToExit) {
      this.clickToExit = false;
      this.timer.addTimeOut(0.2, "nextstep");
      // this.goToNextStep();
      return;
    }

    if (this.mouseDown) return;

    this.mouseDown = true;
    this.pointerId = event.pointerId;

    if (this.dragDetails != null && this.timeToDragCompletionAnnoucement != 0) {
      this.timeToDragCompletionAnnoucement = this.time + this.dragDetails["repeatOnCompleteInterval"];
    }

    this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
    this.mouse.y = -(event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;

    this.raycaster.setFromCamera(this.mouse, this.camera);

    var intersects = this.raycaster.intersectObjects(this.scene.children);

    if (this.audio_on_inactivity) {
      this.audio_on_inactivity_start = this.time;
    }

    if (intersects.length > 0) {
      let intersectName = intersects[0].object.name;
      //console.log(intersectName);

      // First preference is to Menu. If any menu click, take action and return
      if (this.menu != null) {
        for (let i = 0; i < this.menu["buttons"].length; ++i) {
          let button = this.menu["buttons"][i];
          // If not toggle, then normal button
          if (button["toggle"] == 0) {
            if (button["normal_id"] == intersectName) {
              if (button["action"] == "replay_audio") {
                if (this.audioInstruction != null) {
                  if (!this.isAnyAudioPlaying()) this.playAudioPath(this.audioInstruction, true);
                }
              } else if (button["action"] == "home") {
                this.removeAllAndLoadBackground();
                this.enableDialog("dialog_home");
              } else if (button["action"] == "back") {
                this.removeAllAndLoadBackground();
                this.enableDialog("dialog_back");
              } else if (button["action"] == "skip") {
                this.removeAllAndLoadBackground();
                let next_level_jump_stepnum = localStorage.getItem(Config.getUniqueKeyFor("previous_step_completed_" + (this.levelNum + 1)));

                if (this.stepIndex < this.jsondata["settings"]["gameplay_stepnum"]) this.enableDialog("dialog_skip_fromhint");
                else if (this.levelNum == 4) this.enableDialog("dialog_skip_fromlastlevel");
                else if (next_level_jump_stepnum) this.enableDialog("dialog_skip_frompreviousleveljump");
                else this.enableDialog("dialog_skip_fromplayagain");
              } else if (button["action"] == "hint") {
                this.cleanAll();

                this.hintFromStep = this.stepIndex;

                if (this.levelNum != 0 && this.stepIndex >= this.jsondata["settings"]["gameplay_stepnum"]) {
                  let image = this.getTextureWithId("button_skip");
                  this.scene.add(image.texture);
                }

                this.skipOnHint = true;

                this.stepIndex = -1;
                this.goToNextStep();
              }

              return;
            }
          } else {
            // Its a toggle button
            if (button["state1_id"] == intersectName) {
              this.removeImage(button["state1_id"]);
              this.addImage(button["state2_id"], button["position"].x, button["position"].y, button["xref"], button["yref"], button["scale"].x, button["scale"].y, button["parent_id"]);
              if (button["action"] == "music_setting") {
                // Mute background music
                if (this.backgroundSound) {
                  this.backgroundSound.pause();
                  let val = 0;
                  localStorage.setItem(Config.getUniqueKeyFor("background_music"), val.toString());
                }
              }

              return;
            }
            if (button["state2_id"] == intersectName) {
              this.removeImage(button["state2_id"]);
              this.addImage(button["state1_id"], button["position"].x, button["position"].y, button["xref"], button["yref"], button["scale"].x, button["scale"].y, button["parent_id"]);
              if (button["action"] == "music_setting") {
                // Enable background music
                if (this.backgroundSound) {
                  this.backgroundSound.play();
                  let val = 1;
                  localStorage.setItem(Config.getUniqueKeyFor("background_music"), val.toString());
                }
              }

              return;
            }
          }
        }
      }

      if (this.dialog != null) {
        for (let i = 0; i < this.dialog["buttons"].length; ++i) {
          let normal_id = this.dialog["buttons"][i]["normal_id"];
          if (normal_id == intersectName) {
            if (this.dialog["buttons"][i]["action"] == "play_again") {
              this.stepIndex = -1;
              if (!this.hintDialogEnabled && this.jsondata["settings"].gameplay_stepnum > 0) this.stepIndex = this.jsondata["settings"].gameplay_stepnum - 1;

              this.replay_only_wrong = false;
              this.hintDialogEnabled = false;
              this.sdk.setPlaycount(this.levelNum);
              this.removeAllAndLoadBackground();
              this.goToNextStep();
              break;
            } else if (this.dialog["buttons"][i]["action"] == "next_level") {
              this.dumpAllPlayableCheckPointsResults();

              this.callback.playNextLevel();
              return;
            } else if (this.dialog["buttons"][i]["action"] == "next_game") {
              this.dumpAllPlayableCheckPointsResults();

              this.callback.playNextGame();
              return;
            } else if (this.dialog["buttons"][i]["action"] == "continue_game") {
              this.wrongnessFromContinue = true;
              this.goToCheckPoint();
              break;
            } else if (this.dialog["buttons"][i]["action"] == "pick_new_game") {
              this.callback.playNextGame();
              return;
            } else if (this.dialog["buttons"][i]["action"] == "previous_level") {
              this.dumpAllPlayableCheckPointsResults();

              localStorage.setItem(Config.getUniqueKeyFor("previous_step_completed_" + this.levelNum), this.stepIndex.toString());
              this.callback.playPreviousLevel();

              break;
            } else if (this.dialog["buttons"][i]["action"] == "exit") {
              console.log("############## Exit 1");
              if (this.hintFromStep != -1) {
                console.log("############## Exit 2");
                this.stepIndex = this.hintFromStep;
                this.hintFromStep = -1;
                this.goToCheckPoint();
                break;
              } else {
                console.log("############## Exit 3");
                localStorage.setItem(Config.getUniqueKeyFor("skipped_level"), "1");
                // localStorage.removeItem("Config.game_unique_id+previous_step_completed_" + this.levelNum);
                // localStorage.removeItem("Config.game_unique_id+previous_step_completed_" + (this.levelNum+1));
                this.callback.playNextLevel();
              }
            } else if (this.dialog["buttons"][i]["action"] == "hint_play_game") {
              this.hintDialogEnabled = false;
              if (this.hintFromStep != -1) {
                this.stepIndex = this.hintFromStep;
                this.hintFromStep = -1;
                this.goToCheckPoint();
                return;
              } else {
              }
            } else if (this.dialog["buttons"][i]["action"] == "lets_play") {
              // This is done here bacause sdk will not be ready when
              // loading first level that is level 0. Instread of calling when scene is loaded
              // we are doing on lets play action. Hoping that, enough time is given
              // to get the event from app and store SDK data.
              if (this.levelNum == 0) this.sdk.setPlaycount(this.levelNum);

              this.disableDialog("dialog_lets_play");
              this.goToNextStep();
              return;
            }
          }
        }
      } else {
        if (this.isAnyAudioPlaying()) {
          this.stopAudioIfStoppable();
          if (this.isAnyAudioPlaying()) {
            return;
          }
        }

        if (this.interaction_audio_anytime.length > 0) {
          let playInteractionAudio = false;
          for (let i = 0; i < this.interaction_audio_anytime.length && !playInteractionAudio; ++i) {
            for (let k = 0; k < this.interaction_audio_anytime[i].ids.length && !playInteractionAudio; ++k) {
              if (this.interaction_audio_anytime[i].ids[k] == intersectName) {
                this.playAudioPath(this.interaction_audio_anytime[i].audioclip);
                playInteractionAudio = true;
                break;
              }
            }
          }
        }

        if (this.toggles.length > 0 && this.toggleClearDuration <= 0) {
          let nToggleActive = 0;
          for (let i = 0; i < this.toggles.length; ++i) {
            let toggle = this.toggles[i];
            let toggleImages = toggle["toggle_action_images"];
            for (let k = 0; k < toggleImages.length; ++k) {
              if (toggleImages[k] == intersectName) {
                toggle["current_state"] = toggle["current_state"] == -1 || toggle["current_state"] == 1 ? 2 : 1;
                this.setToggleImages(toggle);
                // Play audio
                if (!Config.debug2) {
                  for (let j = 0; j < toggle["audio_paths"].length; ++j) {
                    this.playAudioPath(toggle["audio_paths"][j]);
                  }
                }

                break;
              }
            }

            if (toggle["current_state"] == 2) {
              nToggleActive += 1;
            }
          }

          if (nToggleActive >= this.toggleResult["state2ForValidity"]) {
            this.addImage(this.toggleResult["validStateAction"], 0, 0, 0, 0, 1, 1, this.toggleResult["invalidStateAction"]);
          } else {
            this.removeImage(this.toggleResult["validStateAction"]);
          }
        }

        if (this.drags.length > 0) {
          for (let i = 0; i < this.drags.length; ++i) {
            if (this.isClickedOnDrag(this.drags[i], intersectName)) {
              this.currentDrag = this.drags[i];

              this.dragDisplacement.x = intersects[0]["point"].x - this.currentDrag["image"].texture.position.x;
              this.dragDisplacement.y = intersects[0]["point"].y - this.currentDrag["image"].texture.position.y;

              // drag image and follower into front
              {
                let image = this.currentDrag["image"];
                let posz = image.texture.position.z;
                this.currentDrag["dest_pos_z"] = posz;
                image.texture.position.z = posz + 30;
              }

              this.currentDrag["followers_dest_pos_z"] = [];
              this.currentDrag["followers_images"] = [];
              for (let f = 0; f < this.currentDrag["followers"].length; ++f) {
                let image = this.getTextureWithId(this.currentDrag["followers"][f]);
                this.currentDrag["followers_images"].push(image);
                let posz = image.texture.position.z;
                this.currentDrag["followers_dest_pos_z"].push(posz);
                image.texture.position.z = posz + 30;
              }

              break;
            }
          }
        }

        if (this.interaction_click_actions != null) {
          console.log("this.time - this.userInteractionDisableTime: ", this.time - this.userInteractionDisableTime);
          console.log("this.userInteractionDisableDuration: ", this.userInteractionDisableDuration);
          if (this.time - this.userInteractionDisableTime >= this.userInteractionDisableDuration) {
            console.log(this.interaction_click_actions);
            console.log("intersectName: ", intersectName);
            for (let i = 0; i < this.interaction_click_actions.length; ++i) {
              let action = this.interaction_click_actions[i];
              if (action["id"] == intersectName) {
                if (Config.debug1 == false) {
                  this.userInteractionDisableTime = this.time;
                  this.userInteractionDisableDuration = action["disableInteractionDuration"];
                }

                let audioPaths = action["audio_paths"];
                if (!Config.debug2) {
                  for (let audioIndex = 0; audioIndex < audioPaths.length; ++audioIndex) this.playAudioPath(audioPaths[audioIndex]);
                }

                let overlayImageId = action["overlayImage"]["id"];
                if (overlayImageId.length > 0) {
                  let overlayImage = this.getTextureWithId(overlayImageId);
                  let pos = overlayImage.texture.position;
                  pos.x = action["overlayImage"]["position"].x;
                  pos.y = action["overlayImage"]["position"].y;

                  let xref = 0;
                  let yref = 0;
                  {
                    let parentImage = this.getTextureWithId(intersectName);
                    pos.x += parentImage.texture.position.x;
                    pos.y += parentImage.texture.position.y;

                    xref = parentImage.xref;
                    yref = parentImage.yref;
                  }

                  overlayImage.setNewPosition(xref, pos.x, yref, pos.y);

                  this.scene.add(overlayImage.texture);
                  this.enabledImages.push(overlayImage);

                  if (action["overlayDuration"] > 0) {
                    this.interaction_overlay = overlayImage;
                    this.interaction_overlayStartTime = this.time;
                    this.interaction_overlayDuration = action["overlayDuration"];
                  }
                }

                console.log(this.completion_action_ids);
                let correct = false;
                // Check if its in the correct actions
                for (let k = 0; k < this.completion_action_ids.length; ++k) {
                  if (this.completion_action_ids[k] == intersectName) {
                    console.log("got it");
                    this.completion_action_ids.splice(k, 1);

                    correct = true;

                    break;
                  }
                }

                if (!correct) {
                  this.addWrongness(this.stepDetails["step_num"]);
                } else {
                  this.addCorrectNess(this.stepDetails["step_num"]);
                }

                console.log("hintJump 1");
                if (this.completion_action_ids.length == 0) {
                  this.interaction_click_actions = null;
                  this.completion_action_ids = null;

                  if (this.stepDetails["correctJump"] && this.stepDetails["correctJump"] > 0) {
                    this.setForNextStepNum(this.stepDetails["correctJump"]);
                    this.timer.addTimeOut(0, "nextstep");
                  } else {
                    this.goToNextStep();
                  }
                } else {
                  console.log("hintJump 2");
                  if (this.stepDetails["hintJump"] && this.stepDetails["hintJump"] > 0) {
                    console.log("hintJump 3");
                    console.log(this.wrongnessTrackerLocal["wrongResults"]);
                    if (this.wrongnessTrackerLocal["wrongResults"] >= this.stepDetails["showHintAfter"]) {
                      console.log("hintJump 4");
                      this.setForNextStepNum(this.stepDetails["hintJump"]);
                      this.timer.addTimeOut(0, "nextstep");
                    }
                  }
                }

                break;
              }
            }
          }
        } else if (this.toggleResult != null) {
          console.log(1);
          if (this.toggleResult["validStateAction"] == intersectName) {
            this.removeImage(this.toggleResult["validStateAction"]);

            let wrongResults = 0;
            // Set right or wrong
            for (let i = 0; i < this.toggles.length; ++i) {
              // Get the toggle index from toggleResult
              let toggleIndex = -1;
              for (let k = 0; k < this.toggleResult["toggles"].length; ++k) {
                if (this.toggleResult["toggles"][k] == this.toggles[i]["guid"]) {
                  toggleIndex = k;
                  break;
                }
              }

              if (this.toggles[i]["current_state"] == -1 || this.toggles[i]["current_state"] == 1) {
                if (this.toggleResult["results"][toggleIndex] == 2) wrongResults += 1;
              } else {
                if (this.toggleResult["results"][toggleIndex] == 1) wrongResults += 1;
              }

              // Check if toggle is untouched, then dont do anything
              if (this.toggles[i]["current_state"] != -1 && this.toggles[i]["current_state"] != 1) {
                // compare with result index
                // Enable right or wrong images
                let result = this.toggleResult["results"][toggleIndex] == this.toggles[i]["current_state"];
                this.setToggleRightWrongImages(this.toggles[i], result);
              }
            }

            console.log("wrongResults: ", wrongResults);
            // Check all results. If matching go to next step after duration
            if (wrongResults == 0) {
              // Check if completed step present. if present move to next or
              if (this.toggleResult["onCompleteStep"] > 0) {
                this.setForNextStepNum(this.toggleResult["onCompleteStep"]);
              }
              this.toggles = [];

              this.timer.addTimeOut(this.toggleResult["resultResetDuration"], "nextstep");
              this.addCorrectNess(this.toggleResult["step_num"]);

              this.toggleResult = null;
            } else {
              // else clear them after duration
              this.toggleClearStart = this.time;
              this.toggleClearDuration = this.toggleResult["resultResetDuration"];

              this.addWrongness(this.toggleResult["step_num"]);

              if (this.wrongnessTrackerLocal != null) {
                if (this.wrongnessTrackerLocal["wrongResults"] >= this.toggleResult["showClueAfterNTries"]) {
                  if (this.toggleResult["onClueStep"] > 0) {
                    this.setForNextStepNum(this.toggleResult["onClueStep"]);
                    this.timer.addTimeOut(0.2, "nextstep");
                  } else {
                    for (let t = 0; t < this.toggles.length; ++t) {
                      let toggleIndex = -1;
                      for (let k = 0; k < this.toggleResult["toggles"].length; ++k) {
                        if (this.toggleResult["toggles"][k] == this.toggles[t]["guid"]) {
                          toggleIndex = k;
                          break;
                        }
                      }
                      if (this.toggleResult["results"][toggleIndex] == 2) {
                        for (let i = 0; i < this.toggles[t]["clue"].length; ++i) {
                          let image = this.getTextureWithId(this.toggles[t]["clue"][i]);
                          this.scene.add(image.texture);
                          this.enabledImages.push(image);
                        }
                      }
                    }
                  }
                }
              }

              if (this.toggleResult["wrongAudioClip"].length > 0) {
                this.playAudioPath(this.toggleResult["wrongAudioClip"]);
              }
            }
          }
        } else if (this.dragDetails != null) {
          if (this.dragDetails["click_action"]["id"] == intersectName || (this.dragDetails["hightlightClickButton"].length > 0 && this.dragDetails["hightlightClickButton"] == intersectName)) {
            this.removeImage(this.dragDetails["click_action"]["id"]);
            if (this.dragDetails["hightlightClickButton"].length > 0) this.removeImage(this.dragDetails["hightlightClickButton"]);

            this.dragDetails = null;
            this.drags = [];
            this.goToNextStep();
          }
        } else if (this.inputWait != null) {
          if (this.inputWait.exitid == intersectName || this.inputWait.exit_highlight_id == intersectName) {
            console.log("Its Exit");
            let ids = this.getDictionary("", this.inputWait.exitid);
            if (ids.length > 0) {
              // Show higlight
              this.addImage(ids[0], 0, 0, 0, 0, 1, 1, intersectName);
              this.removeImageOnTimerIds.push(ids[0]);
              this.timer.addTimeOut(0.8, "remove_image_timer");
            }

            this.removeImage(this.inputWait.exitid);
            this.removeImage(this.inputWait.exit_highlight_id);
            this.timer.removeTimeOutAll("add_input_highlight_after_complete");

            if (this.enableInputClue && this.enableInputClue.keyHighlightIndexInDictionary != -1) {
              let clueHighlight = this.enableInputClue.inputClueHighlightIds[this.currentInput];
              this.removeImage(clueHighlight);
            }

            this.inputWait = null;
            this.currentInput = -1;
            this.enableInputClue = null;

            this.goToNextStep();
          } else if (this.inputWait.deleteid == intersectName) {
            console.log("Its delete");
            let ids = this.getDictionary("", this.inputWait.deleteid);
            if (ids.length > 0) {
              // Show higlight
              this.addImage(ids[0], 0, 0, 0, 0, 1, 1, intersectName);
              this.removeImageOnTimerIds.push(ids[0]);
              this.timer.addTimeOut(0.8, "remove_image_timer");
            }
            if (this.currentInput != -1) {
              let input = this.getInput(this.inputWait.inputids[this.currentInput]);
              if (input.dynamicKey.length > 0) {
                let image = this.getTextureWithId(input.dynamicKey);
                image.dictionaryKey = "";
                this.removeImage(input.dynamicKey);
                input.dynamicInput = "";
                input.dynamicKey = "";

                this.currentInput -= 1;
              } else if (this.currentInput > 0) {
                this.currentInput -= 1;
                input = this.getInput(this.inputWait.inputids[this.currentInput]);
                if (input.dynamicKey.length > 0) {
                  let image = this.getTextureWithId(input.dynamicKey);
                  image.dictionaryKey = "";
                  this.removeImage(input.dynamicKey);
                }
                input.dynamicInput = "";
                input.dynamicKey = "";
              }
            }
          } else {
            let input = this.getInput(this.inputWait.inputids[this.currentInput]);
            if (input) {
              let ids = this.getDictionary("", intersectName);
              if (ids.length > 0) {
                input["dynamicInput"] = intersectName;
                // Highlight for a 0.8 second
                if (input.keyHighlightIndexInDictionary >= 0) {
                  this.addImage(ids[input.keyHighlightIndexInDictionary], 0, 0, 0, 0, 1, 1, intersectName);
                  this.removeImageOnTimerIds.push(ids[input.keyHighlightIndexInDictionary]);
                  this.timer.addTimeOut(0.8, "remove_image_timer");
                }

                if (input.dynamicKey.length > 0) {
                  let image = this.getTextureWithId(input.dynamicKey);
                  if (image) image.dictionaryKey = "";
                  this.removeImage(input.dynamicKey);
                  input.dynamicKey = "";
                }
                if (input.indexInDictionary < ids.length) {
                  input.dynamicKey = ids[input.indexInDictionary];
                  this.removeImage(input.dynamicKey);
                  let image = this.getTextureWithId(input.dynamicKey);
                  this.addImage(input.dynamicKey, input.position.x, input.position.y, input.xref, input.yref, 1, 1, input.parentid);
                  image.dictionaryKey = intersectName;
                }
              }
            }
          }
        }
      }
    }
  };

  isAnyAudioPlaying = () => {
    for (let i = 0; i < this.audioPlayers.length; ++i) {
      if (this.audioPlayers[i].isAudioPlaying()) return true;
    }
    return false;
  };

  stopAudioIfStoppable = () => {
    for (let i = 0; i < this.audioPlayers.length; ++i) {
      let audio = this.audioPlayers[i];
      if (audio.isAudioPlaying() && audio.stoppable) {
        this.audioPlayers.splice(i, 1);
        audio.dispose();
        break;
      }
    }
  };

  cleaupAudioEnded = () => {
    for (let i = 0; i < this.audioPlayers.length; ++i) {
      if (this.audioPlayers[i].isAudioPlaying() == false) {
        let audio = this.audioPlayers[i];
        this.audioPlayers.splice(i, 1);
        audio.dispose();
        break;
      }
    }
  };

  onDocumentMouseMove = (event) => {
    if (!this.mouseDown || this.pointerId != event.pointerId) return;

    this.mouse.x = (event.clientX / this.renderer.domElement.clientWidth) * 2 - 1;
    this.mouse.y = -(event.clientY / this.renderer.domElement.clientHeight) * 2 + 1;

    this.raycaster.setFromCamera(this.mouse, this.camera);

    var intersects = this.raycaster.intersectObjects(this.scene.children);

    if (this.currentDrag != null) {
      let xref = this.currentDrag["image"].xref;
      let yref = this.currentDrag["image"].yref;
      let x = intersects[0]["point"].x - this.dragDisplacement.x;
      let y = intersects[0]["point"].y - this.dragDisplacement.y;
      this.currentDrag["image"].setNewPosition(xref, x, yref, y);

      this.setPositionForFollowers(this.currentDrag["followers_images"], x, y, xref, yref);
    }
  };

  onDocumentMouseUp = (event) => {
    console.log("-------- onDocumentMouseUp ------------");

    if (!this.mouseDown || this.pointerId != event.pointerId) return;

    if (this.currentDrag != null) {
      let positions = this.currentDrag["positions"];
      let pos = new THREE.Vector3(0, 0, this.currentDrag["image"].texture.position.z);
      let grabbed = false;
      for (let i = 0; i < positions.length; ++i) {
        pos.x = positions[i].x;
        pos.y = positions[i].y;
        if (pos.distanceTo(this.currentDrag["image"].texture.position) < this.currentDrag["grabDistance"]) {
          let occupied = false;
          // Check if any other drags already present in pos
          for (let d = 0; d < this.drags.length; ++d) {
            if (this.drags[d]["image"].texture.name != this.currentDrag["image"].texture.name) {
              let dpos = new THREE.Vector3(this.drags[d]["image"].texture.position.x, this.drags[d]["image"].texture.position.y, pos.z);
              if (pos.distanceTo(dpos) < this.currentDrag["grabDistance"]) {
                occupied = true;
                break;
              }
            }
          }
          if (!occupied) {
            grabbed = true;

            if (this.currentDrag["replaceid"] && this.currentDrag["replaceid"].length > 0) {
              if (this.currentDrag["image"].texture.name != this.currentDrag["replaceid"]) {
                let posOldZ = this.currentDrag["image"].texture.position.z;
                this.currentDrag["image"].texture.position.z = this.currentDrag["dest_pos_z"];
                this.removeImage(this.currentDrag["id"]);

                this.addImage(this.currentDrag["replaceid"], pos.x, pos.y, 0, 0, 1, 1, null);

                let image = this.getTextureWithId(this.currentDrag["replaceid"]);
                this.currentDrag["image"] = image;
                let posz = cloneDeep(image.texture.position.z);
                this.currentDrag["dest_pos_z"] = posz;
                image.texture.position.z = posOldZ;
              }
            }

            this.currentDrag["image"].setNewPosition(0, pos.x, 0, pos.y);
            this.currentDrag["image"].texture.position.z = this.currentDrag["dest_pos_z"];

            this.setPositionForFollowers(this.currentDrag["followers_images"], pos.x, pos.y, 0, 0);
            this.setZPositionForFollowers(this.currentDrag["followers_images"], this.currentDrag["followers_dest_pos_z"]);

            break;
          }
        }
      }

      let positionsOccupied = 0;
      let pos2 = new THREE.Vector3(0, 0, 0);
      for (let i = 0; i < positions.length; ++i) {
        pos2.x = positions[i].x;
        pos2.y = positions[i].y;
        for (let d = 0; d < this.drags.length; ++d) {
          pos2.z = this.drags[d]["image"].texture.position.z;
          if (pos2.distanceTo(this.drags[d]["image"].texture.position) < this.currentDrag["grabDistance"]) {
            positionsOccupied += 1;
            break;
          }
        }
      }

      if (!grabbed) {
        let orig_position = this.currentDrag["orig_position"];

        this.currentDrag["image"].setNewPosition(0, orig_position.x, 0, orig_position.y);
        this.currentDrag["image"].texture.position.z = this.currentDrag["dest_pos_z"];

        console.log("bringing back to original position: ", this.currentDrag["image"].texture.position );

        this.setPositionForFollowers(this.currentDrag["followers_images"], orig_position.x, orig_position.y, 0, 0);
        this.setZPositionForFollowers(this.currentDrag["followers_images"], this.currentDrag["followers_dest_pos_z"]);
      }

      if (this.dragDetails["type"] == "drop_complete_and_wait_for_click_action") {
        let clickimage = this.dragDetails["click_action"];
        let capacity = this.dragDetails["minNumberOfDragsTobeFilled"];
        if (capacity == 0) {
          capacity = this.dragDetails["drags"].length;
        }
        if (positionsOccupied != capacity) {
          this.removeImage(clickimage["id"]);
        } else {
          let image = this.getTextureWithId(clickimage["id"]);
          if (clickimage["parentid"].length > 0) this.addImage(clickimage["id"], clickimage.position.x, clickimage.position.y, clickimage.xref, clickimage.yref, clickimage.scale.x, clickimage.scale.y, clickimage["parentid"]);
          else this.addImage(clickimage["id"], image.texture.position.x, image.texture.position.y, image.xref, image.yref, image.texture.scale.x, image.texture.scale.y, null);

          // Its interaction drag step is active
          // check for this.dragDetails
          if (this.dragDetails["repeatOnCompleteAudioClip"] && this.dragDetails["repeatOnCompleteAudioClip"].length > 0) {
            this.timeToDragCompletionAnnoucement = this.time + this.dragDetails["repeatOnCompleteInterval"];
          }
        }
      } else if (this.dragDetails["type"] == "drop_and_wait_for_click_action") {
        let clickimage = this.dragDetails["click_action"];
        if (positionsOccupied == 0) {
          this.removeImage(clickimage["id"]);
        } else {
          let image = this.getTextureWithId(clickimage["id"]);
          if (clickimage["parentid"].length > 0) this.addImage(clickimage["id"], clickimage.position.x, clickimage.position.y, clickimage.xref, clickimage.yref, clickimage.scale.x, clickimage.scale.y, clickimage["parentid"]);
          else this.addImage(clickimage["id"], image.texture.position.x, image.texture.position.y, image.xref, image.yref, image.texture.scale.x, image.texture.scale.y, null);
        }
      } else if (this.dragDetails["type"] == "drop_complete_and_continue") {
        this.dragDetails = null;
        this.drags = [];
        this.goToNextStep();
      } else if (this.dragDetails["type"] == "drop_complete_destination_and_continue") {
        if (positionsOccupied == this.dragDetails["drags"].length) {
          this.dragDetails = null;
          this.drags = [];
          this.goToNextStep();
        }
      }

      if (this.verifyPositions != null) {
        let positionsOccupied = 0;
        for (let i = 0; i < this.verifyPositions["destinations"].length; ++i) {
          let dest = this.verifyPositions["destinations"][i];
          let image = this.getTextureWithId(dest["rightid"]);
          let pos = new THREE.Vector3(0, 0, image.texture.position.z);
          pos.x = dest["position"].x;
          pos.y = dest["position"].y;
          if (image.texture.position.distanceTo(pos) < 5) {
            positionsOccupied += 1;
          }
        }
      }
    }

    this.mouseDown = false;
    this.currentDrag = null;
  };

  setPositionForFollowers = (followers_images, x, y, xref, yref) => {
    for (let i = 0; i < followers_images.length; ++i) {
      let image = followers_images[i];
      image.setNewPosition(xref, x, yref, y);
    }
  };

  setZPositionForFollowers = (followers_images, followers_dest_pos_z) => {
    for (let i = 0; i < followers_images.length; ++i) {
      let image = followers_images[i];
      image.texture.position.z = followers_dest_pos_z[i];
    }
  };

  onMenuBarLoadedSuper = () => {
    if (localStorage.getItem(Config.getUniqueKeyFor("level1progress"))) {
      let progress = 0;
      if (this.sdk.getCurrentLevel() == 1) progress = parseFloat(localStorage.getItem(Config.getUniqueKeyFor("level1progress")));
      else if (this.sdk.getCurrentLevel() > 1) progress = 1.0;
      this.menuBar.level1ProgressBar.setProgress(progress);
    }
    if (localStorage.getItem(Config.getUniqueKeyFor("level2progress"))) {
      let progress = 0;
      if (this.sdk.getCurrentLevel() == 2) progress = parseFloat(localStorage.getItem(Config.getUniqueKeyFor("level2progress")));
      else if (this.sdk.getCurrentLevel() > 2) progress = 1.0;
      this.menuBar.level2ProgressBar.setProgress(progress);
    }
    if (localStorage.getItem(Config.getUniqueKeyFor("level3progress"))) {
      let progress = 0;
      if (this.sdk.getCurrentLevel() == 3) progress = parseFloat(localStorage.getItem(Config.getUniqueKeyFor("level3progress")));
      else if (this.sdk.getCurrentLevel() > 3) progress = 1.0;
      this.menuBar.level3ProgressBar.setProgress(progress);
    }
    if (localStorage.getItem(Config.getUniqueKeyFor("level4progress"))) {
      let progress = 0;
      if (this.sdk.getCurrentLevel() == 4) progress = parseFloat(localStorage.getItem(Config.getUniqueKeyFor("level4progress")));
      this.menuBar.level4ProgressBar.setProgress(progress);
    }
  };

  updateSuper = (delta) => {
    this.timer.update();
    this.mascot.update(delta);

    let i = 0;
    while (i < this.lerpingObjs.length) {
      this.lerpingObjs[i].doLerp(delta);
      i += 1;
    }

    i = 0;
    while (i < this.blinkingObjs.length) {
      this.blinkingObjs[i].doBlinking(delta);
      i += 1;
    }
  };

  pause = (value) => {
    this.clock.getDelta();
    this.timer.pause(value);
    for (let i = 0; i < this.audioPlayers.length; ++i) {
      this.audioPlayers[i].pause(value);
    }
  };

  onHomeActionDialogPickNewGame = () => {
    this.homeActionDialog.dispose();
    this.homeActionDialog = null;

    this.sdk.saveDataOnExit();
  };

  onHomeActionDialogContinueGame = () => {
    this.homeActionDialog.dispose();
    this.homeActionDialog = null;

    this.setEnableEvents(true);
    if (this.dragControls) {
      this.dragControls.dispose();
      this.dragControls = null;
    }
    this.goToCheckPoint();
  };

  showHintCompleteDialog = () => {
    this.hintClicked = false;
    this.hintClickedStep = -1;
    this.setEnableEvents(false);
    this.audio.stop();
    this.clearPlayArea();
    this.hintCompleteDialog = new HintCompleteDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound, this);
  };

  onHintCompleteDialogPlayAgain = () => {
    this.hintCompleteDialog.dispose();
    this.hintCompleteDialog = null;

    this.stopAll();
    this.step = -1;
    this.goToNextStep();

    this.setEnableEvents(true);
  };

  onHintCompleteDialogPlayGame = () => {
    this.hintCompleteDialog.dispose();
    this.hintCompleteDialog = null;

    this.goToCheckPoint();

    // this.menuBar.enableBack(true);
    // this.menuBar.enableHint(true);
    // if (this.sdk.getIfLevelIsCompleted(this.levelNum) == 0) {
    //     this.menuBar.enableSkip(false);
    // }

    this.setEnableEvents(true);
  };

  onSkipAction = () => {
    this.showSkipActionDialog();
  };

  showSkipActionDialog = () => {
    this.setEnableEvents(false);
    this.audio.stop();
    if (this.hand) this.scene.remove(this.hand.texture);
    this.clearPlayArea();
    this.timer.clearAll();
    this.checkpoint = this.step;
    if (this.dragControls) {
      this.dragControls.dispose();
      this.dragControls = null;
    }
    this.waitingForTap = false;
    this.comboTaps = "";
    this.skipActionDialog = new SkipActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound, this);
  };

  onSkipActionDialogExit = () => {
    this.skipActionDialog.dispose();
    this.skipActionDialog = null;

    this.setEnableEvents(true);
    if (localStorage.getItem(Config.getUniqueKeyFor("presentStep"))) {
      localStorage.setItem(Config.getUniqueKeyFor("skipClicked"), true);
    }
    if (this.hintClicked) {
      this.checkpoint = this.hintClickedStep;
      this.goToCheckPoint();
    } else {
      this.callback.playNextLevel();
    }
  };

  onSkipActionDialogContinue = () => {
    this.skipActionDialog.dispose();
    this.skipActionDialog = null;
    this.SkipContinueClicked = true;
    if (this.hintClicked) {
      this.hintContinueClicked = true;
      // if (this.sdk.getIfLevelIsCompleted(this.levelNum) == 0) this.menuBar.enableSkip(false);
    }
    this.setEnableEvents(true);
    this.goToCheckPoint();
  };

  onBackAction = () => {
    this.showBackActionDialog();
  };

  showBackActionDialog = () => {
    this.setEnableEvents(false);
    this.audio.stop();
    this.clearPlayArea();
    this.timer.clearAll();
    this.checkpoint = this.step;
    this.comboTaps = "";
    this.backActionDialog = new BackActionDialog(this.loadingManager, this.scene, this.camera, this.renderer, this.sound, this);
    this.stopAll();
  };

  onBackActionDialogPreviousLevel = () => {
    this.backActionDialog.dispose();
    this.backActionDialog = null;

    this.setEnableEvents(true);
    localStorage.setItem(Config.getUniqueKeyFor("presentStep"), this.step);
    this.callback.playPreviousGame();
  };

  onBackActionDialogContinue = () => {
    this.backActionDialog.dispose();
    this.backActionDialog = null;

    this.setEnableEvents(true);

    this.goToCheckPoint();
  };

  endOfStep = (step) => {};

  goToStep = (step) => {
    console.log("goToStep: " + step);
    this.stepFunctions[step]();
  };

  playAudioPath = (path, stoppable = false) => {
    this.currentAudioPath = path;

    for (let i = 0; i < this.audioPlayers.length; ++i) {
      if (this.audioPlayers[i].soundfile == path) {
        return;
      }
    }

    if (this.audioPlayers.length > 10) {
      return;
    }

    let audio = new SAudio(this, this.listener, stoppable);
    this.audioPlayers.push(audio);
    audio.play(path);
  };

  playBackgroundMusic = (loop, soundfile, volume) => {
    this.audioLoader.load(soundfile, (buffer) => {
      if (this.backgroundSound.isPlaying) this.backgroundSound.stop();
      this.backgroundSound.setBuffer(buffer);
      this.backgroundSound.setLoop(loop);
      this.backgroundSound.setVolume(volume);

      let val = localStorage.getItem(Config.getUniqueKeyFor("background_music"));
      console.log("val: ", val);
      val = val == null ? 1 : parseInt(val);
      if (val == 1) {
        this.backgroundSound.play();
      }
    });
  };

  onTimeOut = (ref) => {
    switch (ref) {
      case "nextstep":
        this.goToNextStep();
        break;

      case "next_confetti":
        this.showConfetti();

        break;

      case "remove_image_timer":
        if (this.removeImageOnTimerIds.length > 0) this.removeImage(this.removeImageOnTimerIds[0]);
        this.removeImageOnTimerIds.splice(0, 1);
        break;
      case "add_input_highlight_after_complete":
        if (this.inputWait) {
          this.addImage(this.inputWait.exit_highlight_id, 0, 0, 0, 0, 1, 1, this.inputWait.exitid);
        }
        break;
    }
  };

  showConfetti = () => {
    let rect = new party.Rect(window.innerWidth / 2, window.innerHeight, 3, 4);
    party.settings.gravity = 300;
    party.confetti(rect, {
      count: 100,
      countVariation: 1,
      angleSpan: 10,
      velocity: 50,
      // yVelocity: 300,
      // yVelocityVariation: 4,
      rotationVelocityLimit: 0,
      scaleVariation: 4.8,
      size: 1.5,
      spread: 20,
      shapes: ["star", "roundedSquare", "square", "rectangle", "circle", "roundedRectangle"],
    });
  };
}

export default SSScene;
