import * as THREE from 'three'
const MeshLine = require('three.meshline').MeshLine;
const MeshLineMaterial = require('three.meshline').MeshLineMaterial;
const MeshLineRaycast = require('three.meshline').MeshLineRaycast;

class SSLine {
    constructor(scene) {        
        this.points = []

        this.line = new MeshLine();
        this.line.setPoints(this.points);
        
        let OPTIONS = {
            color: new THREE.Color( 0x0000ff ),
            opacity: 1,
            lineWidth: 5,
            side: THREE.DoubleSide
        };
        this.material = new MeshLineMaterial(OPTIONS);
        this.mesh = new THREE.Mesh(this.line, this.material);
        scene.add(this.mesh);
    }

    dispose(){

    };

    draw = () => {
        this.line.setPoints(this.points);
    }
};

export default SSLine;
